                  <el-input v-model="filters.name2" placeholder="联系电话"></el-input>
                </el-form-item>

                <el-form-item>
                  <el-button type="primary"  v-on:click="searchData">搜索</el-button>
                </el-form-item>
                <div class="el-icon" style="width:300px;float:right;">
                    <el-button plain icon="el-icon-refresh"></el-button>
                    <el-button plain icon="el-icon-download"></el-button>
                    <el-button plain icon="el-icon-setting"></el-button>
                </div>
              </div>
              <el-form-item>
                <router-link to="/AgencyInformation/NewInstitutions">
                  <span style="cursor:pointer;" v-on:click="changeMsg">
                      <el-button type="primary" @click="handleAdd" icon="el-icon-plus" >
                          新增
                      </el-button>
                  </span>
                </router-link>
              </el-form-item>
              <el-form-item>
                <el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
              </el-form-item>
              <el-form-item>
                <el-button plain  >停止营业</el-button>
              </el-form-item>
              <el-form-item>
                <el-button plain >审核</el-button>
              </el-form-item>
            </el-form>
          </el-row>
        <!--列表-->
          <el-row>
              <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                  <el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
                  <el-table-column type="selection" width="55" >
                  </el-table-column>
                  <el-table-column prop="orgName"  label="机构名称" width="120" >
                  </el-table-column>
                  <el-table-column prop="userName" label="负责人姓名" width="120" >
                  </el-table-column>
                  <el-table-column prop="" label="性别" width="100" :formatter="formatSex" >
                  </el-table-column>

                  <!-- <el-table-column prop="age" label="年龄" width="100" >
                  </el-table-column> -->

                  <el-table-column prop="userPhone" label="联系电话" width="200" >
                  </el-table-column>
                  <el-table-column prop="" label="教练总数" width="90" >
                  </el-table-column>
                  <el-table-column prop="" label="课程类目数" width="90" >
                  </el-table-column>
                  <el-table-column prop="" label="场馆数" width="70" >
                  </el-table-column>
                  <el-table-column prop="" label="机构状态" width="120" prop="state">
                  </el-table-column>
                  <el-table-column  label="审核状态" width="120" prop="auditState">
                  </el-table-column>


                  <el-table-column label="操作" align="center" width="250" class="el_tep" style="padding-right:15px;">
                    <template scope="scope" >
                      <span style="cursor:pointer;">编辑</span>
                      <span class="el_tep_d"></span>
                      <el-dropdown trigger="click">
                          <span class="el-dropdown-link" style="cursor:pointer;">
                            更多<i class="el-icon-arrow-down el-icon--right"></i>
                          </span>
                          <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item >
                              <router-link to="/AgencyInformation/CoachInformation">
                                <span style="cursor:pointer;" v-on:click="changeMsg">
                                  教练信息
                                </span>
                              </router-link>
                            </el-dropdown-item>
                            <el-dropdown-item >课程信息</el-dropdown-item>
                            <el-dropdown-item >发票信息</el-dropdown-item>
                            <el-dropdown-item >删除</el-dropdown-item>
                          </el-dropdown-menu>
                        </el-dropdown>
                    </template>
                  </el-table-column>
