</template>

<script>
  import util from '../../common/js/util'
  import service from '../../common/js/http-service'
  import {getOrgList,getState ,getOrgDelete} from '../../api/api';

  export default {
    data() {
      return {
         isVisible:true,
          currentPage: 1,
          pageSize:2,
         // options: [{
         //        value: '选项1',
         //        label: '营业中'
         //      }, {
         //        value: '选项2',
         //        label: '暂停营业'
         //      },{
         //        value: '选项3',
         //        label: '注销'
         //      }],
         options:[],


         options1: [{
                value: '选项1',
                label: '通过'
              }, {
                value: '选项2',
                label: '等待审核'
              },{
                value: '选项3',
                label: '不通过'
              },],
              value: '',
        filters: {
          name: ''
        },
        users: [{
          // phone:'15827001324',
          // frequency:'0',
          // RegistrationTime:'2018/02/15 18:30:30'
        }],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [],//列表选中列

        editFormVisible: false,//编辑界面是否显示
        editLoading: false,
        editFormRules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ]
        },
        //编辑界面数据
        editForm: {
          id: 0,
          name: '',
          sex: -1,
          age: 0,
          birth: '',
          addr: ''
        },
      }
    },
    methods: {

    
      // getSclect(){
      //   this.axios({
      //     method:'post',
      //     url:'http://okycz.com/org/list',
      //   })
      //   .then(function(response){
      //     alert(response.data);
      //   }).bind(this)
      //   .catch(function(error){
      //     console.log(error)
      //   })
      // },

      changeMsg(){
        this.isVisible=false;
      },
      rowClass({row,rowIndex}){
        console.log(0)
        return 'background:#f1f2f7'
      },
      

      selsChange: function (sels) {
        this.sels = sels;
      },
       //每页显示数据量变更
        handleSizeChange: function(val) {
            this.pageSize = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        //页码变更
        handleCurrentChange: function(val) {
            this.currentPage = val;
            this.loadData( this.currentPage, this.pageSize);
        },
         getState:function(){
            getState().then(resp=>{
                this.states=resp.data.data;
            });
        },
       searchData:function(){
            this.loadData( this.currentPage, this.pageSize);
        },
        loadData: function(currentPage, pageNum){
            var _this = this ;
            CourseDelete( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
            })).then(resp=>{
                // _this.roles = ;
                _this.users = resp.data.data;
                _this.totalCount =resp.data.totalCount;
            });
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
      //批量删除
       deleteSelection(){
            this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '删除',
                cancelButtonText: '取消'
            }).then(() => {
                let ids = [];
                const params = new URLSearchParams();
                this.multipleSelection.forEach(item=>{
                    ids.push(item['roleId'])
                    params.append("ids",item['roleId']);
                })
               

                getOrgDelete(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message.success("删除成功！");
                        this.searchData();
                    } else {
                        this.$message.error("删除失败！");
                    }
                });
            });

        },

    },
    mounted() {
       
       this.getState();

      var _this = this ;  //很重要！！
      getOrgList({}).then(resp=>{
        console.log(resp);
        _this.users = resp.data.data;
      },err=>{
        console.log(err);
      }).catch(err=>{
        console.log(err);
      })
    
    }
  }

</script>

<style scoped>
  .sect{
    border:20px solid #f1f2f7;
  }
  .el_f_h{
    min-width:1350px;
  }
  .toolbar{
    min-width:1375px;
    background-color:white;
  }
  <!-- .breadcrumb-container .title{
    display:none;
  } -->
  .el-breadcrumb{
    float:left;
  }
  .cell span{
    color:#0094ff;
    padding-right:5px;
  }
  .el-dropdown{
    background-color:transparent;
    padding-left:3px;
    //border-left:1px solid #E6E9ED;
  }
  .el_tep_d{
    display:inline-block;
    margin-bottom:-2px;
    height:14px;
    border-left:1px solid #E6E9ED;
    }
  .el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
    <!-- background-color:blue !important; -->
    font-size:25px;
  }
  .el_tep{
    padding-right:15px;
  }
</style>



