<!-- 发票信息 -->
<template>
  <section>
    <!--工具条-->
    <el-row :span="24" class="toolbar" style="padding-bottom: 0px;background-color:white;padding:10px 20px 0 20px;">
      <el-form :inline="true" :model="filters">
        <div class="el_f_h">
          <el-form-item>
             <el-select v-model="value" placeholder="全部开具类型" style="width:160px;">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" >
              </el-option>
             </el-select>
          </el-form-item>
    
          <el-form-item >
              <el-select v-model="value" placeholder="全部发票类型" style="width:160px;">
                <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" >
              </el-option>
             </el-select>
          </el-form-item>
          <el-form-item style="width:150px;">
            <el-input v-model="filters.name2" placeholder="发票抬头"></el-input>
          </el-form-item>


          

          <el-form-item>
            <el-button type="primary" v-on:click="getUsers">搜索</el-button>
          </el-form-item>
          <div class="el-icon" style="width:300px;float:right;"> 
              <el-button plain icon="el-icon-refresh"></el-button>
              <el-button plain icon="el-icon-download"></el-button>
              <el-button plain icon="el-icon-setting"></el-button>  
          </div> 
        </div>
        <el-form-item>
          <el-button type="primary" @click="handleAdd" icon="el-icon-plus">新增</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" @click="handleAdd" style="background-color:#EE1313;">批量删除</el-button>
        </el-form-item>
        <el-form-item>
          <el-button plain  @click="handleAdd">授权</el-button>
        </el-form-item>
          <el-form-item>
          <el-button plain  @click="handleAdd">注销</el-button>
        </el-form-item>
      </el-form>
    </el-row>

    <!--列表-->
      <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
              <el-table-column type="selection" width="55" >
              </el-table-column>
              <el-table-column  label="发票抬头" width="300" >
              </el-table-column>
              <el-table-column  label="开局类型" width="300" >
              </el-table-column>
              <el-table-column  label="发票类型" width="150"  >
              </el-table-column>

              

              <el-table-column label="操作" align="center" width="250" class="el_tep" style="padding-right:15px;">
                <template scope="scope" >
                  <span style="cursor:pointer;">编辑</span>
                  <span class="el_tep_d"></span>
                  <span style="cursor:pointer;">删除</span>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>


    <!--工具条-->
    <el-col :span="24" class="toolbar" style="width:94%">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
      <!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
      <el-pagination
        background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage2"
          :page-sizes="[10, 30, 50]"
          :page-size="10"
          layout="sizes, prev, pager, next"
          :total="1000"  style="float:right;">
        </el-pagination>
      
    </el-col>
  </section>
</template>

<script>
  import util from '../../common/js/util'
  //import NProgress from 'nprogress'
  import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

  export default {
    data() {
      return {
           options: [{
                value: '选项1',
                label: '个人'
              }, {
                value: '选项2',
                label: '企业'
              }],
              value: '',
            options2: [{
                value: '选项1',
                label: '增值税普通发票'
              }, {
                value: '选项2',
                label: '增值税专用发票'
              }],
              value: '',
        filters: {
          name: ''
        },
        users: [{
          phone:'15827001324',
          frequency:'0',
          RegistrationTime:'2018/02/15 18:30:30'
        }],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [],//列表选中列

        editFormVisible: false,//编辑界面是否显示
        editLoading: false,
        editFormRules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ]
        },
        //编辑界面数据
        editForm: {
          id: 0,
          name: '',
          sex: -1,
          age: 0,
          birth: '',
          addr: ''
        },

        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ]
        },
        //新增界面数据
        addForm: {
          name: '',
          sex: -1,
          age: 0,
          birth: '',
          addr: ''
        }

      }
    },
    methods: {
      rowClass({row,rowIndex}){
        console.log(0)
        return 'background:#f1f2f7'  
      },
      //性别显示转换
      formatSex: function (row, column) {
        return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
      },
       handleSizeChange(val) {
        this.pagesize = val;
            return val;
          },
      handleCurrentChange(val) {
        this.page = val;
        this.getUsers();
      },
      //获取用户列表
      getUsers() {
        let para = {
          page: this.page,
          name: this.filters.name
        };
        console.log(111111);
        console.log(para);
        this.listLoading = true;
        //NProgress.start();
        getUserListPage(para).then((res) => {
          console.log(22222);
          console.log(res.data);
          this.total = res.data.total;
          this.users = res.data.users;
          this.listLoading = false;
          //NProgress.done();
        });
      },
      //删除
      handleDel: function (index, row) {
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { id: row.id };
          removeUser(para).then((res) => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getUsers();
          });
        }).catch(() => {

        });
      },
     
      selsChange: function (sels) {
        this.sels = sels;
      },
      //批量删除
     
    },
    mounted() {
      this.getUsers();
    }
  }

</script>

<style scoped>
  .el_f_h{
    min-width:1350px;
  }
  .toolbar{
    min-width:1375px;
    background-color:white;
  }
  <!-- .breadcrumb-container .title{
    display:none;
  } -->
  .el-breadcrumb{
    float:left;
  }
  .cell span{
    color:#0094ff;
    padding-right:5px;
  }
  .el-dropdown{
    background-color:transparent;
    padding-left:3px;
    //border-left:1px solid #E6E9ED;
  }
  .el_tep_d{
    display:inline-block;
    margin-bottom:-2px;
    height:14px;
    border-left:1px solid #E6E9ED;
    }
  .el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
    <!-- background-color:blue !important; -->
    font-size:25px;
  }
  .el_tep{
    padding-right:15px;
  }
</style>


