				<el-row :span="24" class="toolbar" style="padding-bottom: 0px;background-color:white;padding:10px 20px 0 20px;">
					<el-form ref="form" :model="form" :inline="true">
						<div class="el_f_h">
						  <el-form-item>
							 <el-select placeholder="全部状态" style="width:120px;" >
							    <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
							 	</el-option>	
							 </el-select>
						  </el-form-item>
						  <el-form-item>
							 <el-select v-model="value" placeholder="全部注册来源" style="width:140px;">
							    <el-option v-for="item in options1" :key="item.value" :label="item.label1" :value="item.value">
							    </el-option>
							  </el-select>
						  </el-form-item>
						  <el-form-item>
							 <el-select v-model="value" placeholder="全部性别" style="width:140px;">
							    <el-option v-for="item in options1" :key="item.value" :label="item.label1" :value="item.value">
							    </el-option>
							  </el-select>
						  </el-form-item>
							<el-form-item style="width:120px;">
								<el-input v-model="filters.name" placeholder="用户姓名"></el-input>
							</el-form-item>
							<el-form-item style="width:150px;">
								<el-input v-model="filters.name2" placeholder="手机号码"></el-input>
							</el-form-item>


							<el-form-item style="width:300px;">
								<el-col :span="11">
									<el-input v-model="filters.name3" placeholder="消费次数从"></el-input>
								</el-col>
								<el-col class="line" :span="2" style="padding-left:10px;">-</el-col>
								<el-col :span="11">
									<el-input v-model="filters.name4" placeholder="至"></el-input>
								</el-col>
							</el-form-item>

							<el-form-item>
								<el-button type="primary" v-on:click="getUsers">搜索</el-button>
							</el-form-item>
							<div class="el-icon" style="width:200px;float:right;"> 
									<el-button plain icon="el-icon-refresh"></el-button>
									<el-button plain icon="el-icon-download"></el-button>
									<el-button plain icon="el-icon-setting"></el-button>	
							</div> 
						</div>
						<el-form-item>
							<el-button type="primary" @click="handleAdd" icon="el-icon-plus">新增</el-button>
						</el-form-item>
						<el-form-item>
							<el-button type="danger" @click="handleAdd" style="background-color:#EE1313;">批量删除</el-button>
						</el-form-item>
						<el-form-item>
							<el-button plain  @click="handleAdd">启用</el-button>
						</el-form-item>
					</el-form>
				</el-row>

				<!--列表-->
					<el-row>
		  				<el-col :span="24">
		  					<div class="grid-content bg-purple-dark">
		  						<el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
									<el-table-column type="selection" width="55" >
									</el-table-column>
									<el-table-column prop="name" label="用户姓名" width="120" >
									</el-table-column>
									<el-table-column prop="sex" label="性别" width="100" :formatter="formatSex" >
									</el-table-column>

									<!-- <el-table-column prop="age" label="年龄" width="100" >
									</el-table-column> -->

									<el-table-column prop="phone" label="手机号码" width="200" >
									</el-table-column>
									<el-table-column prop="birth" label="出生日期" width="120" >
									</el-table-column>

									<!-- <el-table-column prop="addr" label="地址" min-width="180" >
									</el-table-column> -->

									<el-table-column prop="frequency" label="消费次数" width="120" >
									</el-table-column>
									<el-table-column prop="RegistrationTime" label="注册时间" width="215" >
									</el-table-column>
									<el-table-column prop="source" label="注册来源" width="215" >
									</el-table-column>
									<el-table-column prop="status" label="用户状态" width="200" >
									</el-table-column>

									<el-table-column label="操作" align="center" width="250" class="el_tep"	style="padding-right:15px;">
										<template scope="scope" >
										 <div>
											<!-- <span style="cursor:pointer;" v-on:click="changeMsg"><router-link to="/table/forms2">编辑</router-link></span> -->
											<span class="el_tep_d">编辑</span>
											<el-dropdown trigger="click">
										      <span class="el-dropdown-link" style="cursor:pointer;">
										        更多<i class="el-icon-arrow-down el-icon--right"></i>
										      </span>
										      <el-dropdown-menu slot="dropdown">
										        <el-dropdown-item><div>商户申请</div></el-dropdown-item>
										        <el-dropdown-item >消费记录</el-dropdown-item>
										        <el-dropdown-item >发票信息</el-dropdown-item>
										        <el-dropdown-item >收件地址</el-dropdown-item>
										        <el-dropdown-item @cllick="handleDel">删除</el-dropdown-item>
										      </el-dropdown-menu>
										    </el-dropdown>
										  </div>
										</template>
									</el-table-column>
								</el-table>
							</div>
						</el-col>
					</el-row>

				<el-row>
					<el-col :span="24">
						<div>
							
							<router-view></router-view>
						</div>
						</el-col>
				</el-row>
				<!--工具条-->
				<el-col :span="24" class="toolbar">
					<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
					<!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
					<el-pagination
					  background
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page.sync="currentPage2"
				      :page-sizes="[10, 30, 50]"
				      :page-size="10"
				      layout="sizes, prev, pager, next"
				      :total="1000"  style="float:right;">
				    </el-pagination>
					
				</el-col>
			</div>
				<el-row>
					<el-col :span="24">
