							<router-view></router-view>
						</div>
					</el-col>
				</el-row>

				
			</section>
		
	</div>
</template>

<script> 
  import util from '../../common/js/util'
  import service from '../../common/js/http-service'

  import {getUserOrgRegister } from '../../api/api';
	export default {
		data() {
			
			return {
				 isVisible:true,
				// type:'A',
				form: {},
				 options1: [{
			          value: '选项1',
			          label: '启用'
			        }, {
			          value: '选项2',
			          label: '禁用'
			        }],
			     value: '',
			    /* options2: [{
			          value: '选项1',
			          label: '小程序'
			        }, {
			          value: '选项2',
			          label: 'IOS'
			        },{
			          value: '选项3',
			          label: 'Android'
			        },{
			          value: '选项4',
			          label: 'PC'
			        }],
			        value: '',
			     options3: [{
			          value: '选项1',
			          label: '男'
			        }, {
			          value: '选项2',
			          label: '女'
			        }],
			        value: '',*/
				filters: {
					name: ''
				},
				users: [{
					phone:'15827001324',
					frequency:'0',
					RegistrationTime:'2018/02/15 18:30:30'
				}],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}
			}
		},
		methods: {
			changeMsg(){
				this.isVisible=false
			},
			rowClass({row,rowIndex}){
				console.log(0)
				return 'background:#f1f2f7'  
			},
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			 handleSizeChange(val) {
			 	this.pagesize = val;
		        return val;
		      },
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				console.log(111111);
				console.log(para);
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					console.log(22222);
					console.log(res.data);
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			console.log(sessionStorage.getItem("user"));
			console.log(sessionStorage.getItem('authorization'));

		  var _this = this ;  //很重要！！
	      getUserOrgRegister({}).then(resp=>{
	        console.log(resp);
	        _this.users = resp.data.data;
	      },err=>{
	        console.log(err);
	      }).catch(err=>{
	        console.log(err);
	      })
		}
	}

</script>

<style scoped>
	.sect{
		border:20px solid #f1f2f7;
	}
	.el_f_h{
		min-width:1350px;
	}
	.toolbar{
		min-width:1375px;
		background-color:white;
	}
	/* .breadcrumb-container .title{
		display:none;
	} */
	.el-breadcrumb{
		float:left;
	}
	.cell span{
		color:#0094ff;
		padding-right:5px;
	}
	.el-dropdown{
		background-color:transparent;
		padding-left:3px;
		//border-left:1px solid #E6E9ED;
	}
	.el_tep_d{
		display:inline-block;
		margin-bottom:-2px;
		height:14px;
		border-left:1px solid #E6E9ED;
		}
	.el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
		<!-- background-color:blue !important; -->
		font-size:25px;
	}
	.el_tep{
		padding-right:15px;
	}
</style>



