<!-- 学员信息 -->
<template>
  <section class="sect">
    <!--工具条-->
    <el-row :span="24" class="toolbar" style="padding-bottom: 0px;background-color:white;padding:10px 20px 0 20px;">
      <el-form :inline="true" :model="filters">
        <div class="el_f_h">
          <el-form-item>
           <el-select v-model="value" placeholder="全部状态" style="width:120px;">
              <el-option v-for="item in states" :key="item.value" :label="item.label" :value="item.value" >
            </el-option>
           </el-select>
          </el-form-item>
          <el-form-item style="width:120px;">
            <el-input v-model="studentUserName"  placeholder="学员姓名"></el-input>
          </el-form-item>
          <el-form-item style="width:120px;">
            <el-input  placeholder="监护人姓名"></el-input>
          </el-form-item>
          <el-form-item style="width:150px;">
            <el-input  placeholder="手机号"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" v-on:click="searchData">搜索</el-button>
          </el-form-item>
          <div class="el-icon" style="width:300px;float:right;"> 
              <el-button plain icon="el-icon-refresh"></el-button>
              <el-button plain icon="el-icon-download"></el-button>
              <el-button plain icon="el-icon-setting"></el-button>  
          </div> 
        </div>
        <el-form-item>
          <el-button type="primary" @click="handleAdd" icon="el-icon-plus">新增</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
        </el-form-item>
        
      </el-form>
    </el-row>

    <!--列表-->
      <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
              <el-table-column type="selection" width="55" >
              </el-table-column>
              <el-table-column prop="studentUserName" label="姓名" width="120" >
              </el-table-column>
              <el-table-column prop="sex" label="性别" width="100" :formatter="formatSex" >
              </el-table-column>

              <!-- <el-table-column prop="age" label="年龄" width="100" >
              </el-table-column> -->

              <el-table-column prop="" label="出生日期" width="200" >
              </el-table-column>
              <el-table-column prop="" label="报名次数" width="90" >
              </el-table-column>
              <el-table-column prop="guardianUserName" label="监护人" width="120" >
              </el-table-column>
              <el-table-column prop="phone" label="手机号码" width="120" >
              </el-table-column>
              <el-table-column prop="currentCourseNum" label="现服务课程数" width="120" >
              </el-table-column>
              <el-table-column prop="state" label="学员状态" width="120" >
              </el-table-column>
              

              <el-table-column label="操作" align="center" width="250" class="el_tep" style="padding-right:15px;">
                <template scope="scope" >
                  <span style="cursor:pointer;">编辑</span>
                  <span class="el_tep_d"></span>
                  <el-dropdown trigger="click">
                      <span class="el-dropdown-link" style="cursor:pointer;">
                        更多<i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item >教练信息</el-dropdown-item>
                        <el-dropdown-item >课程信息</el-dropdown-item>
                        <el-dropdown-item >发票信息</el-dropdown-item>
                        <el-dropdown-item >删除</el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>


    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
      <!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
      <el-pagination
        background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[10, 30, 50]"
          :page-size="pageSize"
          layout="sizes, prev, pager, next"
          :total="totalCount"  style="float:right;">
        </el-pagination>
      
    </el-col>

   
  </section>
</template>

<script>  
  import util from '../../common/js/util'
  import service from '../../common/js/http-service'

  import {getOrgStudentList,getState , StudentDelete } from '../../api/api';


  export default {
    data() {
      return {
        isVisible:true,
         options: [{
                value: '选项1',
                label: '正常'
              }, {
                value: '选项2',
                label: '注销'
              }],
              value: '',
          
        filters: {
          name: ''
        },
        users: [{
          phone:'15827001324',
          frequency:'0',
          RegistrationTime:'2018/02/15 18:30:30'
        }],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [],//列表选中列

          studentUserName:'',
       

      }
    },
    methods: {
      rowClass({row,rowIndex}){
        console.log(0)
        return 'background:#f1f2f7'  
      },
   
      selsChange: function (sels) {
        this.sels = sels;
      },
        //每页显示数据量变更
          handleSizeChange: function(val) {
              this.pageSize = val;
              this.loadData( this.currentPage, this.pageSize);
          },
          //页码变更
          handleCurrentChange: function(val) {
              this.currentPage = val;
              this.loadData( this.currentPage, this.pageSize);
          },
          getState:function(){
              getState().then(resp=>{
                  this.states=resp.data.data;
              });
          },

          searchData:function(){
              this.loadData( this.currentPage, this.pageSize);
          },
          loadData: function(currentPage, pageNum){
              var _this = this ;
              CourseDelete( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
                studentUserName:this.studentUserName
              })).then(resp=>{
                  // _this.roles = ;
                  _this.users = resp.data.data;
                  _this.totalCount =resp.data.totalCount;
              });
          },
          toggleSelection(rows) {
              if (rows) {
                  rows.forEach(row => {
                      this.$refs.multipleTable.toggleRowSelection(row);
                  });
              } else {
                  this.$refs.multipleTable.clearSelection();
              }
          },
          handleSelectionChange(val) {
              this.multipleSelection = val;
          },
          //批量删除
          deleteSelection(){
              this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
                  distinguishCancelAndClose: true,
                  confirmButtonText: '删除',
                  cancelButtonText: '取消'
              }).then(() => {
                  let ids = [];
                  const params = new URLSearchParams();
                  this.multipleSelection.forEach(item=>{
                      ids.push(item['roleId'])
                      params.append("ids",item['roleId']);
                  })
                  StudentDelete(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                      if (resp.resultCode == 0) {
                          this.$message.success("删除成功！");
                          this.searchData();
                      } else {
                          this.$message.error("删除失败！");
                      }
                  });
              });

          },
 
    },
    mounted() {
      this.getState();
       var _this = this ;  //很重要！！

      getOrgStudentList({}).then(resp=>{

        console.log(resp);
        _this.users = resp.data.data;
      },err=>{
        console.log(err);
      }).catch(err=>{
        console.log(err);
      })
    }
  }

</script>

<style scoped>
  .sect{
    border:20px solid #f1f2f7;
  }
  .el_f_h{
    min-width:1350px;
  }
  .toolbar{
    min-width:1375px;
    background-color:white;
  }
  <!-- .breadcrumb-container .title{
    display:none;
  } -->
  .el-breadcrumb{
    float:left;
  }
  .cell span{
    color:#0094ff;
    padding-right:5px;
  }
  .el-dropdown{
    background-color:transparent;
    padding-left:3px;
    //border-left:1px solid #E6E9ED;
  }
  .el_tep_d{
    display:inline-block;
    margin-bottom:-2px;
    height:14px;
    border-left:1px solid #E6E9ED;
    }
  .el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
    <!-- background-color:blue !important; -->
    font-size:25px;
  }
  .el_tep{
    padding-right:15px;
  }
</style>


