<!-- 新增课程 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="课程名称">
					<el-input v-model="form.courseName" style="width:98%" placeholder="给课程起个名字"></el-input>
				</el-form-item>
				<el-form-item label="所属机构">
					 <el-select v-model="value5" multiple placeholder="请选择" style="width:98%">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>

				<el-form-item label="课程类目" >
					<el-select v-model="form.courseType" placeholder="文化类" style="width:165px;">
						 <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
					<el-select v-model="form.region" placeholder="选择课程项目" style="width:165px;">
						 <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
					<el-select v-model="form.region" placeholder="正式课" style="width:167px;">
						 <el-option v-for="item in options4" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="开班设置" >
					<el-select v-model="form.region" placeholder="选择班种" style="width:165px;">
						 <el-option v-for="item in options5" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
					<el-select v-model="form.region" placeholder="选择开班条件" style="width:165px;">
						 <el-option v-for="item in options6" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>


				
<!-- 难点，待完成 -->
				<el-form-item label="上课时间">
					

				</el-form-item>

				<el-form-item label="课程课时">
					

				</el-form-item>

				
				
				<el-form-item label="场馆" >
					<el-select v-model="value5" multiple placeholder="请选择" style="width:98%">
					    <el-option
					      v-for="item in options7"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>

				</el-form-item>
				<el-form-item label="宣传图片">
					<el-upload
					  action="https://jsonplaceholder.typicode.com/posts/"
					  list-type="picture-card"
					  :on-preview="handlePictureCardPreview"
					  :on-remove="handleRemove">
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>

				</el-form-item>
				<el-form-item label="教练介绍">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit">提交</el-button>
					<el-button plain>重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
	import {CourseAdd} from "../../../api/api"
	export default {
		data() {
			return {
				 dialogImageUrl: '',
       			 dialogVisible: false,
				 pickerOptions1: {
			          disabledDate(time) {
			            return time.getTime() > Date.now();
			          },
			          shortcuts: [{
			            text: '今天',
			            onClick(picker) {
			              picker.$emit('pick', new Date());
			            }
			          }, {
			            text: '昨天',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24);
			              picker.$emit('pick', date);
			            }
			          }, {
			            text: '一周前',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', date);
			            }
			          }]
			        },
			        value1: '',
				 options: [{
	                value: '选项1',
	                label: '爱博体育'
	              }, {
	                value: '选项2',
	                label: '企业'
	              }],
	              value5: [],
	            options2: [{
	                value: '选项1',
	                label: '文化类'
	              }, {
	                value: '选项2',
	                label: '体育类'
	              },{
	                value: '选项3',
	                label: '艺术类'
	              }],
	              value: '',
	            options3: [{
	                value: '选项1',
	                label: '游泳'
	              }, {
	                value: '选项2',
	                label: '击剑'
	              }],
	              value: '',
	             options4: [{
	                value: '选项1',
	                label: '正式课'
	              }, {
	                value: '选项2',
	                label: '体验课'
	              }],
	              value: '',
	             options5: [{
			          value: '选项1',
			          label: '大班(10人)'
			        }, {
			          value: '选项2',
			          label: '小班(3人)'
			        },{
			          value: '选项2',
			          label: '私教(1对1)'
			        }],
	              value: '',
	             options6: [{
	                value: '选项1',
	                label: '1人立即开班'
	              }, {
	                value: '选项2',
	                label: '指定人数开班'
	              }, {
	                value: '选项3',
	                label: '全部满员开班'
	              }],
	              value: '',
	             options7: [{
	                value: '选项1',
	                label: '杭州游泳馆'
	              }, {
	                value: '选项2',
	                label: '临安游泳馆'
	              }],
	              value5: [],  
				form: {
					courseName: '',
					region: '',
					courseType:'',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					imageUrl: '',
					num1:1,
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			  handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
		       handleChange(value) {
		        console.log(value);
		      },
		      onSubmit:function() {
                CourseAdd({courseName:this.form.courseName,courseType:this.form.courseType}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message({
                            message:"添加成功！",
                            type: "success"
                        });
                        this.showTableView();
                    } else {
                        this.$message({
                            message:resp.message,
                            type: 'error'
                        });
                    }
                });
			  },
		    }
		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


