<!-- 组织架构 -->
<template>
  <section class="sect">
    <!--工具条-->
    <el-row :span="24" class="toolbar" style="padding-bottom: 0px;background-color:white;padding:10px 20px 0 20px;">
      <el-form :inline="true" :model="filters">
        <div class="el_f_h">
          <el-form-item>
             <el-select v-model="value" placeholder="全部状态" style="width:120px;">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
             </el-select>
          </el-form-item>
    
          <el-form-item style="width:120px;">
            <el-input v-model="filters.name" placeholder="组织编号"></el-input>
          </el-form-item>
          <el-form-item style="width:150px;">
            <el-input v-model="filters.name2" placeholder="组织名称"></el-input>
          </el-form-item>


          

          <el-form-item>
            <el-button type="primary" v-on:click="searchData">搜索</el-button>
          </el-form-item>
          <div class="el-icon" style="width:300px;float:right;"> 
              <el-button plain icon="el-icon-refresh"></el-button>
              <el-button plain icon="el-icon-download"></el-button>
              <el-button plain icon="el-icon-setting"></el-button>  
          </div> 
        </div>
        <el-form-item>
          <el-button type="primary"  icon="el-icon-plus">新增</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
        </el-form-item>
        <el-form-item>
          <el-button plain  >禁用</el-button>
        </el-form-item>
      </el-form>
    </el-row>

    <!--列表-->
      <el-row>
          <el-col :span="6">
            <div class="grid-content bg-purple-light">
                <el-header style="background-color: rgba(245, 246, 250, 1);"><i class="bg-purple-light-i">组织架构树</i></el-header>
                <el-main>
                    <el-input v-model="input" placeholder="请输入内容"  ></el-input>
                    <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>

                </el-main>

            </div>
          </el-col>
          <el-col :span="18">
            <div class="grid-content bg-purple-dark">
              <el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
              <el-table-column type="selection" width="55" >
              </el-table-column>
              <el-table-column  label="组织编号" width="120" prop="departmentCode">
              </el-table-column>
              <el-table-column  label="组织名称" width="120" prop="departmentName">
              </el-table-column>
              <el-table-column  label="组织层级" width="100"  prop="level">
              </el-table-column>


              <el-table-column  label="上级组织" width="200" prop="parentDepartmentName">
              </el-table-column>
              <el-table-column prop="" label="状态" width="120" prop="state">
              </el-table-column>
              

              <el-table-column label="操作" align="center" width="250" class="el_tep" style="padding-right:15px;">
                <template scope="scope" >
                  <span style="cursor:pointer;">查看</span>
                  <span class="el_tep_d"></span>
                  <span style="cursor:pointer;">编辑</span>
                  <span class="el_tep_d"></span>
                  <span style="cursor:pointer;">删除</span>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>


    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
      <!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
      <el-pagination
        background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage2"
          :page-sizes="[10, 30, 50]"
          :page-size="10"
          layout="sizes, prev, pager, next"
          :total="1000"  style="float:right;">
        </el-pagination>
      
    </el-col>

    
  </section>
</template>

<script>
  import util from '../../common/js/util'
  import service from '../../common/js/http-service'
  import {getOrgDepartmentList,getState,DepartmentDelete} from '../../api/api';
  import axios from 'axios';
  import qs from 'qs'

  export default {
    data() {
      return {
           data: [{
          label: '优成长',
          children: [{
            label: '营销中心',
            children: [{
              label: '三级 2-1-1'
            }]
          }, {
            label: '人力资源部',
            children: [{
              label: '三级 2-2-1'
            },]
          },{
            label: '产品中心',
            children: [{
              label: '三级 2-2-1'
            },]
          },{
            label: '市场部',
            children: [{
              label: '三级 2-2-1'
            },]
          },{
            label: '技术中心',
            children: [{
              label: '后台部',
            },{
              label: '设计部',
            },{
              label: '前端部',
            },{
              label: '研发部',
            },{
              label: '测试部',
            }]
          },{
            label: '运营中心',
            children: [{
              label: '三级 2-2-1'
            },]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
         input: '',
         options: [{
                value: '选项1',
                label: '启用'
              }, {
                value: '选项2',
                label: '禁用'
              }],
              value: '',
           options: [{
                value: '选项1',
                label: '小程序'
              }, {
                value: '选项2',
                label: 'IOS'
              },{
                value: '选项3',
                label: 'Android'
              },{
                value: '选项4',
                label: 'PC'
              }],
              value: '',
           options: [{
                value: '选项1',
                label: '男'
              }, {
                value: '选项2',
                label: '女'
              }],
              value: '',
        filters: {
          name: ''
        },
        users: [{
          phone:'15827001324',
          frequency:'0',
          RegistrationTime:'2018/02/15 18:30:30'
        }],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [],//列表选中列

      

      }
    },
    methods: {
      rowClass({row,rowIndex}){
        console.log(0)
        return 'background:#f1f2f7'  
      },
     
      selsChange: function (sels) {
        this.sels = sels;
      },
     
           //每页显示数据量变更
        handleSizeChange: function(val) {
            this.pageSize = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        //页码变更
        handleCurrentChange: function(val) {
            this.currentPage = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        getState:function(){
            getState().then(resp=>{
                this.states=resp.data.data;
            });
        },

        searchData:function(){
            this.loadData( this.currentPage, this.pageSize);
        },
        loadData: function(currentPage, pageNum){
            var _this = this ;
            getOrgDepartmentList( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
                
            })).then(resp=>{
                // _this.roles = ;
                _this.roles = resp.data.data;
                _this.totalCount =resp.data.totalCount;
            });
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        //批量删除
        deleteSelection(){
            this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '删除',
                cancelButtonText: '取消'
            }).then(() => {
                let ids = [];
                const params = new URLSearchParams();
                this.multipleSelection.forEach(item=>{
                    ids.push(item['roleId'])
                    params.append("ids",item['roleId']);
                })
                

                DepartmentDelete(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message.success("删除成功！");
                        this.searchData();
                    } else {
                        this.$message.error("删除失败！");
                    }
                });
            });

        },
    },
    mounted() {
      this.getState();

      var _this = this ;  //很重要！！
      getOrgDepartmentList({}).then(resp=>{
          console.log(resp);
          _this.users = resp.data.data;
        },err=>{
          console.log(err);
        }).catch(err=>{
          console.log(err);
        })
    }
  }

</script>

<style scoped>
  .sect{
    border:20px solid #f1f2f7;
  }
  .el_f_h{
    min-width:1350px;
  }
  .toolbar{
    min-width:1375px;
    background-color:white;
  }
  <!-- .breadcrumb-container .title{
    display:none;
  } -->
  .el-breadcrumb{
    float:left;
  }
  .cell span{
    color:#0094ff;
    padding-right:5px;
  }
  .el-dropdown{
    background-color:transparent;
    padding-left:3px;
    //border-left:1px solid #E6E9ED;
  }
  .el_tep_d{
    display:inline-block;
    margin-bottom:-2px;
    height:14px;
    border-left:1px solid #E6E9ED;
    }
  .el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
    <!-- background-color:blue !important; -->
    font-size:25px;
  }
  .el_tep{
    padding-right:15px;
  }
  .bg-purple-light{
    border:1px solid gray;
  }
  .bg-purple-light-i{
     line-height: 58px;
    padding-left: 86px;
    font-size: 16px;
  }
     
</style>


