<!-- 新增教练 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :rules="rules" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="教练姓名" prop="userName">
					<el-input v-model="form.userName" style="width:98%" placeholder="填写教练的姓名"></el-input>
				</el-form-item>
				<el-form-item label="所属机构">
					 <el-select v-model="form.valueInstitutions" multiple placeholder="请选择" style="width:98%">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>

				<el-form-item label="性别" prop="gender">
					<el-select v-model="form.gender" placeholder="请选择性别" style="width:200px;">
						 <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="请选择2寸登记照上传" prop="iconFile">
					<el-upload
					  class="avatar-uploader"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload"
					  v-model="form.photo">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="手机号码" prop="phone">
					<el-input v-model="form.phone" style="width:98%" placeholder="填写教练的手机号码"></el-input>
				</el-form-item>
				<el-form-item label="出生日期" prop="birthday">
					<div class="block">
					    <span class="demonstration"></span>
					    <el-date-picker
					      v-model="form.birthday"
					      type="date"
					      placeholder="选择日期">
					    </el-date-picker>
				    </div>
				</el-form-item>
				<el-form-item label="专业特长" prop="">
					 <el-select v-model="form.professional" multiple placeholder="请选择" style="width:98%">
					    <el-option
					      v-for="item in options3"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>
				<el-form-item label="上课时间">
					<el-checkbox-group v-model="form.checkList">
					    <el-checkbox label="周一"></el-checkbox>
					    <el-checkbox label="周二"></el-checkbox>
					    <el-checkbox label="周三"></el-checkbox>
					    <el-checkbox label="周四" ></el-checkbox>
					    <el-checkbox label="周五" ></el-checkbox>
					    <el-checkbox label="周六" ></el-checkbox>
					    <el-checkbox label="周日" ></el-checkbox>
				  	</el-checkbox-group>
				</el-form-item>
				<el-form-item label="从业开始时间">
					<div class="block">
					    <span class="demonstration"></span>
					    <el-date-picker
					      v-model="value1"
					      type="date"
					      placeholder="选择日期">
					    </el-date-picker>
				    </div>
				</el-form-item>
				<el-form-item label="学历" >
					<el-select v-model="form.region" placeholder="选择学历" style="width:200px;">
						 <el-option v-for="item in options4" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="工作性质" >
					<el-select v-model="form.region" placeholder="选择工作性质" style="width:200px;">
						 <el-option v-for="item in options5" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="履历" >


				</el-form-item>

				
				<el-form-item label="教练介绍">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit">提交</el-button>
					<el-button plain>重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
  import {getOrgEmployeeAddCoach} from '../../../api/api';

	export default {
		data() {
			return {
				checkList: [],
				 pickerOptions1: {
			          disabledDate(time) {
			            return time.getTime() > Date.now();
			          },
			          shortcuts: [{
			            text: '今天',
			            onClick(picker) {
			              picker.$emit('pick', new Date());
			            }
			          }, {
			            text: '昨天',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24);
			              picker.$emit('pick', date);
			            }
			          }, {
			            text: '一周前',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', date);
			            }
			          }]
			        },
			        value1: '',
				 options: [{
	                value: '选项1',
	                label: '爱博体育'
	              }, {
	                value: '选项2',
	                label: '企业'
	              }],
	              value: '',
	            options2: [{
	                value: '选项1',
	                label: '男'
	              }, {
	                value: '选项2',
	                label: '女'
	              }],
	              value5: [],
	            options3: [{
	                value: '选项1',
	                label: '足球'
	              }, {
	                value: '选项2',
	                label: '篮球'
	              }],
	              value5: [],
	             options4: [{
			          value: '选项1',
			          label: '教授'
			        }, {
			          value: '选项2',
			          label: '博士'
			        }, {
			          value: '选项3',
			          label: '研究生'
			        }, {
			          value: '选项4',
			          label: '本科'
			        }, {
			          value: '选项5',
			          label: '大专'
			        },{
			          value: '选项6',
			          label: '高中'
			        },{
			          value: '选项7',
			          label: '其他学历'
			        }],
	              value: '',
	             options5: [{
	                value: '选项1',
	                label: '全职'
	              }, {
	                value: '选项2',
	                label: '兼职'
	              }],
	              value: '',
				form: {
					userName: '',
					valueInstitutions:'',
					gender: '',
					photo:'',
					phone:'',
					birthday:'',
					professional:'',
					checkList:'',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					imageUrl: '',
					num1:1,
				},
				rules:{
					userName:[
						{required: true,message:'请填写姓名',trigger: 'blur'},
					],
					valueInstitutions:[
						{type:'number',required: true,message:'请选择',trigger: 'change'},
					],
					gender:[
						{type:'text',required: true,message:'请选择性别',trigger: 'change'},
					],
					
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			 onSubmit:function() {
                getOrgEmployeeAddCoach({userName:this.form.userName,valueInstitutions:this.form.valueInstitutions,
                    gender:this.form.gender,phone:this.form.phone,}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message({
                            message:"添加成功！",
                            type: "success"
                        });
                        this.showTableView();
                    } else {
                        this.$message({
                            message:resp.message,
                            type: 'error'
                        });
                    }
                });
			  },
            back:function () {
                this.showTableView();
            },
			 handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
			
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('上传头像图片只能是 JPG 格式!');
		        }
		        if (!isLt2M) {
		          this.$message.error('上传头像图片大小不能超过 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      },
		      // NewCoachSubmit(formName){
		      // 	 this.$refs[formName].validate(valide=>{
		      // 	 	if(valide){
		      // 	 		console.log('提交成功')
		      // 	 	}else{
		      // 	 		console.log('提交失败')
		      // 	 	}
		      // 	 })
		      // }
		    },

		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


