<template>
	<div>
		<el-row :gutter="20">
		   <el-col :span="12" :offset="6">
				<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
					<el-form-item label="子账号信息" >
						<el-input v-model="form.name" style="width:140px;padding-right:10px;"></el-input>
						<el-select v-model="form.region" placeholder="请选择活动区域" style="width:200px;">
							<el-option label="区域一" value="shanghai"></el-option>
							<el-option label="区域二" value="beijing"></el-option>
						</el-select>
						<el-input v-model="form.name" style="width:140px;padding-left:10px;"></el-input>
					</el-form-item>
					<el-form-item label="活动时间">
						<el-col :span="11" style="padding-left:0px;">
							<el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
						</el-col>
						<el-col class="line" :span="2">-</el-col>
						<el-col :span="11">
							<el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
						</el-col>
					</el-form-item>
					<el-form-item label="活动地点">
						<el-input v-model="form.name" style="width:98%"></el-input>
					</el-form-item>
					<el-form-item label="即时配送">
						<el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
					</el-form-item>
					<el-form-item label="活动性质">
						<el-checkbox-group v-model="form.type">
							<el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
							<el-checkbox label="地推活动" name="type"></el-checkbox>
							<el-checkbox label="线下主题活动" name="type"></el-checkbox>
							<el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item label="特殊资源">
						<el-radio-group v-model="form.resource">
							<el-radio label="线上品牌商赞助"></el-radio>
							<el-radio label="线下场地免费"></el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="活动形式">
						<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="addaa">立即创建</el-button>
						<el-button @click.native.prevent>取消</el-button>
					</el-form-item>
				</el-form>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			},
			addaa() {
				this.$router.push({ path: '/user-agreement' });
			}
		}
	}

</script>


