<script>
  import { requestLogin,getOrgList,setGlobalAuthorization} from '../api/api';
  //import NProgress from 'nprogress'
  export default {
    data() {
      return {
        codeImg: process.env.API_ROOT+'kaptcha?'+Math.random(),
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          radio: '1',
        },

        isVisible:true,
        logining: false,
        registerVisible: true,
        loginVisible: false,
        ruleForm2: {
          account: 'platform',
          checkPass: '123',
          // orgName:'',
          kaptcha:"8888",
            // orgId:""
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      updateCode() {
        this.codeImg = process.env.API_ROOT+'kaptcha?'+Math.random();
      },
     //  querySearchAsync(queryString, cb) {
     //    getOrgList({orgName:queryString}).then((res) => {
     //        var results = [];
     //        res.data.data.forEach(function(currentValue, index, arr){
     //            results.push({"value":currentValue.orgName,"orgId":currentValue.orgId});
     //        });
     //        cb(results);
     //    });
     // },
     //
     // handleSelect(item) {
     //      this.ruleForm2.orgId  = item.orgId;
     // },
      /*changeMsg(){
        this.isVisible=false
      },*/
      regist() {
        this.registerVisible = false;
        this.loginVisible = true;
      },
      gotoUserAgree() {
        this.$router.push({ path: '/user-agreement' });
      },
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      doLogin(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            this.logining = true;
            //NProgress.start();
            var loginParams = { username: this.ruleForm2.account, password: this.ruleForm2.checkPass
                ,loginType:0,kaptcha:this.ruleForm2.kaptcha};
            requestLogin(loginParams).then(postResp => {
              this.logining = false;
              //NProgress.done();
                //
                console.log(postResp);
                var authorization = postResp.headers.authorization;
              let { message, resultCode } = postResp.data;

              if (resultCode !== 0) {
                this.$message({
                  message: message,
                  type: 'error'
                });
              } else {
                  // console.log(data);
                sessionStorage.setItem('user', JSON.stringify(postResp.data.data));
                sessionStorage.setItem('authorization',authorization);
                  setGlobalAuthorization(authorization);

                this.$router.push({ path: '/RegisteredUsers' });
              }
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }

</script>

