<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				<!-- {{collapsed?'':sysName}} -->
				<div class="table_l" >
					<div class="t_img" ><img src="../assets/u2615.png" style="width:45px;height:45px;border-width:0px;position:absolute;left:-13px;top:-2px;">
					</div>
					<div class="t_login1" style=" border-width:0px;position:absolute;left:55px;top:-27px;width:65px; white-space:nowrap;font-size:18px;"><p>禾苗乐教</p></div>
					<div class="t_login2" style=" border-width:0px;position:absolute;left:55px;top:0px;width:65px; white-space:nowrap;font-size:14px;"><p>成长无忧，优成长</p></div>

				</div>
			</el-col>
			<el-col :span="5">
				<div class="tools" @click.prevent="collapse">
					<i class="fa fa-align-justify"></i>
				</div>
			</el-col>
			<el-col :span="14" class="userinfo">
				<div class="u_info_div" style="display:inline-block;width:250px;">
					<div><i class="el-icon-search"></i><span>搜索</span></div>
					<div><i class="el-icon-bell"></i><span style="background-color:red;color:white;border-radius:39%;padding:3px;">32</span></div>
					<div><i class="el-icon-question"></i><span>帮助</span></div>
				</div>
				<el-dropdown trigger="hover">
					<span class="el-dropdown-link userinfo-inner" style="color:gray;"><img :src="this.sysUserAvatar" /> {{sysUserName}}</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item ><i class="el-icon-warning" style="padding-right:10px;"></i><router-link to="/PersonalData" style="text-decoration:none;">个人资料</router-link></el-dropdown-item>
						<el-dropdown-item><i class="el-icon-setting" style="padding-right:10px;"></i><router-link to="/ChangePassword" style="text-decoration:none;">修改密码</router-link></el-dropdown-item> 
						<el-dropdown-item><i class="el-icon-view" style="padding-right:10px;"></i>工作台</el-dropdown-item>
						<el-dropdown-item divided @click.native="logout" style="padding-right:10px;"><i class="el-icon-sort"></i>退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<aside :class="collapsed?'menu-collapsed':'menu-expanded'">
				<!--导航菜单-->
				<el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect"
					 unique-opened router v-show="!collapsed">
					<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
						<el-submenu :index="index+''" v-if="!item.leaf">
							<template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
							<el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden">{{child.name}}</el-menu-item>
						</el-submenu>
						<el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
					</template>
				</el-menu>
				<!--导航菜单-折叠后-->
				<ul class="el-menu el-menu-vertical-demo collapsed" id="el-menu-ul" v-show="collapsed" ref="menuCollapsed" >
					<li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item">
						<template v-if="!item.leaf">
							<div class="el-submenu__title" style="padding-left: 20px;color:white;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
							<ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"> 
								<li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
							</ul>
						</template>
						<template v-else>
							<li class="el-submenu">
								<div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
							</li>
						</template>
					</li>
				</ul>
			</aside>
			 <el-container>
				<el-header style="height:50px;">
					<el-row class="el_row">
				  		<el-breadcrumb separator="/" class="breadcrumb-inner" style="float:left;">
								<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
									{{ item.name }}
								</el-breadcrumb-item>
						</el-breadcrumb>
					</el-row>
				</el-header>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<!-- 面包屑 -->
					<!--<el-col :span="24" class="breadcrumb-container">
						<strong class="title" style="display:none;">{{$route.name}}</strong>
						 <el-breadcrumb separator="/" class="breadcrumb-inner" style="float:left;">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name }}
							</el-breadcrumb-item>
						</el-breadcrumb> 
					</el-col>-->
					<el-col :span="24" class="content-wrapper">
						<transition name="fade" mode="out-in">
							<router-view ></router-view>
						</transition>
					</el-col>
				</div>
			</section>
		  </el-container>
		</el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				sysName:'',
				collapsed:false,
				sysUserName: '',
				sysUserAvatar: '',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			},
			handleopen() {
				//console.log('handleopen');
			},
			handleclose() {
				//console.log('handleclose');
			},
			handleselect: function (a, b) {
			},
			//退出登录
			logout: function () {
				var _this = this;
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					sessionStorage.removeItem('user');
					_this.$router.push('/login');
				}).catch(() => {

				});


			},
			//折叠导航栏
			collapse:function(){
				this.collapsed=!this.collapsed;
			},
			showMenu(i,status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			}
		},
		mounted() {
			var user = sessionStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.sysUserName = user.name || '';
				this.sysUserAvatar = user.avatar || '';
			}

		},
        computed: {
            key() {
                return this.$route.name !== undefined? this.$route.name + +new Date(): this.$route + +new Date()
            }
        }
	}

</script>

<style scoped lang="scss">
	@import '~scss_vars';
	
	.container {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		.header {
			height: 60px;
			line-height: 60px;
			background: white;
			color:#fff;
			border-bottom:1px solid #E6E9ED;
			.userinfo {
				text-align: right;
				.userinfo-inner {
					cursor: pointer;
					color:#fff;
					img {
						width: 40px;
						height: 40px;
						border-radius: 20px;
						margin: 10px 0px 10px 10px;
						float: right;
					}
				}
			}
			.logo {
				//width:230px;
				height:60px;
				font-size: 22px;
				padding-left:20px;
				padding-right:20px;
				border-color: rgba(238,241,146,0.3);
				border-right-width: 1px;
				border-right-style: solid;
				background-color:#333744;
				border-radius:0;
				img {
					width: 40px;
					float: left;
					margin: 10px 10px 10px 18px;
				}
				.txt {
					color:#fff;
				}
			}
			.logo-width{
				width:230px;
			}
			.logo-collapse-width{
				width:60px
			}
			.tools{
				padding: 0px 23px;
				width:14px;
				height: 60px;
				line-height: 60px;
				cursor: pointer;
			}
		}
		.main {
			display: flex;
			// background: #324057;
			position: absolute;
			top: 60px;
			bottom: 0px;
			overflow: hidden;
			border-radius:0;
			aside {
				flex:0 0 230px;
				width: 230px;
				background-color:rgba(51,55,68,1);
				// position: absolute;
				// top: 0px;
				// bottom: 0px;
				.el-menu{
					//height: 100%;
					background-color:rgba(66, 72, 90, 1);
					width:230px;
				}
				.collapsed{
					width:60px;
					.item{
						position: relative;
					}
					.submenu{
						position:absolute;
						top:0px;
						left:60px;
						z-index:99999;
						height:auto;
						display:none;
					}

				}
			}
			.menu-collapsed{
				flex:0 0 60px;
				width: 60px;
			}
			.menu-expanded{
				flex:0 0 230px;
				width: 230px;
			}
			.content-container {
				
				background-color: #f1f2f7;
				flex:1;
				// position: absolute;
				// right: 0px;
				// top: 0px;
				// bottom: 0px;
				// left: 230px;
				overflow-y: scroll;
				padding: 0px;
				.breadcrumb-container {
					//margin-bottom: 15px;
					.title {
						width: 200px;
						float: left;
						color: #475669;
					}
					.breadcrumb-inner {
						float: right;
					}
				}
				.content-wrapper {
					background-color: #fff;
					box-sizing: border-box;
				}
			}
		}
	}
</style>
<style>
	.el-submenu__title{
		color:white;
	}
	.el-submenu__title:hover{
		background-color: rgba(33,196,224,1);
	}
	.el-menu{
		background-color: #333744;
	}
	.el-menu .el-menu-item{
		color:white;
	}
	.el-menu .el-menu-item:hover{
		background-color: rgba(74,80,98,1);
	}
	.u_info_div div{
		float: left;
		font-size: 18px;
		padding-right:20px;
	}
	.u_info_div div span{
		color:#5E5E5E;
	}
	.u_info_div div i{
		color:#333;
	}
	.el-dropdown{
		position:absolute;
		min-width: 95px;
	}
	.el_row{
		top:20px;
	}
</style>


