<!-- 角色管理 -->
<template>
  <section>
      <div v-if="isVisible">
    <!--工具条-->
    <el-row :span="24" class="toolbar" style="padding-bottom: 0px;background-color:white;padding:10px 20px 0 20px;">
      <el-form :inline="true" :model="filters">
        <div class="el_f_h">
          <el-form-item>
           <el-select v-model="state" placeholder="全部状态" style="width:120px;">
              <el-option v-for="item in states" :key="item.value" :label="item.label" :value="item.value" >
            </el-option>
           </el-select>
          </el-form-item>
    
          <el-form-item style="width:120px;">
            <el-input v-model="roleCode" placeholder="角色编号"></el-input>
          </el-form-item>
          <el-form-item style="width:150px;">
            <el-input v-model="roleName" placeholder="角色名称"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" v-on:click="searchData" >搜索</el-button>
          </el-form-item>
          <div class="el-icon" style="width:300px;float:right;"> 
              <el-button plain icon="el-icon-refresh"></el-button>
              <el-button plain icon="el-icon-download"></el-button>
              <el-button plain icon="el-icon-setting"></el-button>  
          </div> 
        </div>
        <el-form-item>
            <router-link to="/RoleManagement/addRole">
				                  <span style="cursor:pointer;" v-on:click="hideTableView">
				                      <el-button type="primary" @click="handleAdd" icon="el-icon-plus" >
				                          新增
				                      </el-button>
				                  </span>
            </router-link>

        </el-form-item>
        <el-form-item>
          <el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
        </el-form-item>
        <!--<el-form-item>-->
          <!--<el-button plain  @click="handleAdd">授权</el-button>-->
        <!--</el-form-item>-->
          <!--<el-form-item>-->
          <!--<el-button plain  @click="handleAdd">注销</el-button>-->
        <!--</el-form-item>-->
      </el-form>
    </el-row>

    <!--列表-->
      <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <el-table :data="roles" :header-cell-style="rowClass"
                        highlight-current-row v-loading="listLoading"
                        @selection-change="handleSelectionChange" style="width: 100%;padding:0 20px 0 20px;">
              <el-table-column type="selection" width="55" />
              <el-table-column type="index" width="60" v-if='show'>
              </el-table-column>

              <el-table-column prop="roleCode" label="角色编号" width="120" />

              <el-table-column prop="roleName"  label="角色名称" width="300" />

                  <el-table-column prop="orgName"  label="所属机构" width="300" />

              <el-table-column prop="addUserName" label="创建者" width="120"  />

              <el-table-column prop="createTime" label="创建时间" width="200" />

              <el-table-column prop="state" label="角色状态" width="120" />

              <el-table-column label="操作" align="center" width="250" class="el_tep" style="padding-right:15px;">
                <template scope="scope" >
                  <span style="cursor:pointer;">查看</span>
                  <span class="el_tep_d"></span>
                  <span style="cursor:pointer;">编辑</span>
                  <span class="el_tep_d"></span>
                  <!--<span style="cursor:pointer;">删除</span>-->
                    <span style="cursor:pointer;">授权</span>
                    <span style="cursor:pointer;">注销</span>
                       <!-- <span style="cursor:pointer;" @click="handleDel(scope.$index, scope.row)">删除</span> -->
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>


    <!--工具条-->
    <!--<el-col :span="12" class="toolbar" align="center">-->
      <!--<div align="center">-->
        <el-col :span="24" class="toolbar">
          <el-pagination
                  background
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="pageSize"
                  layout="prev, pager, next"
                  :total="totalCount" style="float:left;">
          </el-pagination>
        </el-col>
    </div>
      <div v-if="isVisibleChild">
          <el-row>
              <el-col :span="24">
                  <router-view></router-view>
              </el-col>
          </el-row>
      </div>

  </section>

</template>


<script>

  // import service from '../../common/js/http-service'
  // import {getStates} from '../../common/js/constants'
  import axios from 'axios';
  import {getPlatformRoleList, getState, deleteRoles} from '../../api/api';
  import qs from 'qs'

  export default {
    data() {
      return {
          isVisible:true,
          isVisibleChild:false,
          totalCount: 0,
          currentPage: 1,

          pageSize:2,
         options: [{
                value: '选项1',
                label: '启用'
              }, {
                value: '选项2',
                label: '禁用'
              }],
              value: '',
           options: [{
                value: '选项1',
                label: '小程序'
              }, {
                value: '选项2',
                label: 'IOS'
              },{
                value: '选项3',
                label: 'Android'
              },{
                value: '选项4',
                label: 'PC'
              }],
              value: '',
           options: [{
                value: '选项1',
                label: '正常'
              }, {
                value: '选项2',
                label: '注销'
              }],
              value: '',
        filters: {
          name: ''
        },
          roles: [{
          phone:'15827001324',
          frequency:'0',
          RegistrationTime:'2018/02/15 18:30:30'
        }],

        listLoading: false,
        sels: [],//列表选中列


          pageSize:10,
          states:[],
          state:[],
          roleCode:'',
          roleName:'',
          orgId:'',
          listLoading:false,
          multipleSelection:[],
          filters: {
            name: ''
          },
          roles:[],
          sels: [],//列表选中列
          // addFormVisible: false,//新增界面是否显示
          // addLoading: false,
          //
          addform: {
              roleName: "",
              roleCode: "",
              roleDesc: ""
          }
      }
    },
    methods: {
        hideTableView(){
            this.isVisible=false;
            this.isVisibleChild=true;
            },
        showTableView:function(){
            this.isVisible=true;
            this.isVisibleChild=false;
        },
      rowClass({row,rowIndex}){
        // console.log(0)
        return 'background:#f1f2f7'  
      },
        //每页显示数据量变更
        handleSizeChange: function(val) {
            this.pageSize = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        //页码变更
        handleCurrentChange: function(val) {
            this.currentPage = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        getState:function(){
            getState().then(resp=>{
                this.states=resp.data.data;
            });
        },

        searchData:function(){
            this.loadData( this.currentPage, this.pageSize);
        },
        loadData: function(currentPage, pageNum){
            var _this = this ;
            getPlatformRoleList( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
                roleName:this.roleName,roleCode: this.roleCode
            })).then(resp=>{
                // _this.roles = ;
                _this.roles = resp.data.data;
                _this.totalCount =resp.data.totalCount;
            });
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        //批量删除
        deleteSelection(){
            this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '删除',
                cancelButtonText: '取消'
            }).then(() => {
                let ids = [];
                const params = new URLSearchParams();
                this.multipleSelection.forEach(item=>{
                    ids.push(item['roleId'])
                    params.append("ids",item['roleId']);
                })
                // let a = qs.stringify({ids: ids}, { indices: false })
                //形式： ids=1&ids=2&id=3
                // let b =qs.stringify({ids: ids}, {arrayFormat: 'indices'})
                //形式： ids[0]=1&aids1]=2&ids[2]=3
                // let c = qs.stringify({ids: ids}, {arrayFormat: 'brackets'})
                //形式：ids[]=1&ids[]=2&ids[]=3
                // let d = qs.stringify({ids: ids}, {arrayFormat: 'repeat'})
                //形式： ids=1&ids=2&id=3

                // console.log(a);
                // console.log(b);
                // console.log(c);
                // console.log(d);

                deleteRoles(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message.success("删除成功！");
                        this.searchData();
                    } else {
                        this.$message.error("删除失败！");
                    }
                });
            });

        },
   
    },
      provide(){
          return{
              showTableView:this.showTableView
          }
      },
    mounted() {
        this.loadData(this.currentPage, this.pageSize);
        this.getState();
        
    }
  }

</script>

<style scoped>
  .el_f_h{
    min-width:1350px;
  }
  .toolbar{
    min-width:1375px;
    background-color:white;
  }
  <!-- .breadcrumb-container .title{
    display:none;
  } -->
  .el-breadcrumb{
    float:left;
  }
  .cell span{
    color:#0094ff;
    padding-right:5px;
  }
  .el-dropdown{
    background-color:transparent;
    padding-left:3px;
    //border-left:1px solid #E6E9ED;
  }
  .el_tep_d{
    display:inline-block;
    margin-bottom:-2px;
    height:14px;
    border-left:1px solid #E6E9ED;
    }
  .el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
    <!-- background-color:blue !important; -->
    font-size:25px;
  }
  .el_tep{
    padding-right:15px;
  }
</style>


