<!-- 新增角色 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="角色名称">
					<el-input v-model="form.roleName" style="width:98%" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="课程名称">
					<el-input v-model="form.roleCode" style="width:98%" placeholder=""></el-input>
				</el-form-item>



                <el-form-item label="备注">
                    <el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"  @click="onSubmit">提交</el-button>
                    <el-button @click.native.prevent>重置</el-button>
                    <el-button @click="back">返回</el-button>
                </el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
    import {addPlatformRole} from "../../../api/api"
	export default {
        inject:['showTableView'],
		data() {
			return {
                form: {
                    roleName: "",
                    roleCode: "",
                    roleDesc: ""
                }
			}
		},
		methods: {
			onSubmit:function() {
                addPlatformRole({roleName:this.form.roleName,roleCode:this.form.roleCode,
                    roleDesc:this.form.roleDesc}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message({
                            message:"添加成功！",
                            type: "success"
                        });
                        this.showTableView();
                    } else {
                        this.$message({
                            message:resp.message,
                            type: 'error'
                        });
                    }
                });
			  },
            back:function () {
                this.showTableView();
            }
           },

		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


