<!-- 新增机构 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" :rules="rules" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="机构名称" >
					<el-input v-model="form.orgName"  style="width:98%" placeholder="请输入您的机构名称"></el-input>
				</el-form-item>
				<el-form-item label="负责人信息" >
					<el-input v-model="form.userName" style="width:140px;padding-right:10px;" placeholder="请输入您的姓名"></el-input>
					<el-select v-model="form.gander" placeholder="请选择性别" style="width:200px;">
							<el-option label="男" value="man"></el-option>
							<el-option label="女" value="woman"></el-option>
					</el-select>
					<el-input v-model="form.userPhone" style="width:140px;padding-left:10px;" placeholder="请输入联系电话"></el-input>
					
					
				</el-form-item>
				<el-form-item label="LOGO">
					<el-upload
					  class="avatar-uploader"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="营业执照">
					<el-upload
					  action="https://jsonplaceholder.typicode.com/posts/"
					  list-type="picture-card"
					  :on-preview="handlePictureCardPreview"
					  :on-remove="handleRemove">
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				
				<el-form-item label="机构介绍">
					<el-input type="textarea"  style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit">提交</el-button>
					<el-button plain @click="back">重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
//   import util from '../../../common/js/util'
//   import service from '../../../common/js/http-service'
  import {getOrgAdd} from '../../../api/api';
	
	export default {
		inject:['showTableView'],
		data() {
			return {
				rules:{
					orgName:[{
						required: true, //是否必填
				        message: '机构名称不能为空', //规则
				        trigger: 'blur'  //何事件触发
					}],
				},
				form: {
					orgName: '',
					userName:'',
					gander:'',
					userPhone:''
					// delivery: false,
					// type: [],
					// resource: '',
					// desc: '',
					// imageUrl: '',
					// num1:1,
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			 onSubmit:function() {
                getOrgAdd({orgName:this.form.orgName,userName:this.form.userName,
                    gander:this.form.gander, userPhone:this.form.userPhone}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message({
                            message:"添加成功！",
                            type: "success"
                        });
                        this.showTableView();
                    } else {
                        this.$message({
                            message:resp.message,
                            type: 'error'
                        });
                    }
                });
			  },
            back:function () {
                this.showTableView();
            },
            

			 handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('上传头像图片只能是 JPG 格式!');
		        }
		        if (!isLt2M) {
		          this.$message.error('上传头像图片大小不能超过 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      }
		    },
		   
		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


