						 </el-select>
					  </el-form-item>
					  <el-form-item>
						 <el-select v-model="value" placeholder="全部学历" style="width:140px;">
						    <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value" >
						    </el-option>
