						</el-form-item>
						<!-- 标签O -->
						<div style="border:1px solid gray;padding:3px;display:inline-block;border-radius:5px;">
							<el-tag
							  :key="tag"
							  v-for="tag in dynamicTags"
							  closable
							  :disable-transitions="false"
							  @close="handleClose(tag)">
							  {{tag}}
							</el-tag>
							<el-input
							  class="input-new-tag"
							  v-if="inputVisible"
							  v-model="inputValue"
							  ref="saveTagInput"
							  size="small"
							  @keyup.enter.native="handleInputConfirm"
							  @blur="handleInputConfirm"
							>
							</el-input>
							<el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
						</div>
						<!-- 标签E -->
						<el-form-item style="width:150px;">
							<el-input v-model="filters.name2" placeholder="手机号码"></el-input>
						</el-form-item>


						

						<el-form-item>
							<el-button type="primary" v-on:click="searchData">搜索</el-button>
						</el-form-item>
						<div class="el-icon" style="width:300px;float:right;"> 
								<el-button plain icon="el-icon-refresh"></el-button>
								<el-button plain icon="el-icon-download"></el-button>
								<el-button plain icon="el-icon-setting"></el-button>	
						</div> 
					</div>
					<el-form-item>
						<router-link to="/TheCoachInformation/NewTrainer">
			                  <span style="cursor:pointer;" v-on:click="changeMsg">
			                      <el-button type="primary" @click="handleAdd" icon="el-icon-plus" >
			                          新增
			                      </el-button>
			                  </span>
	                	</router-link>
					</el-form-item>
					<el-form-item>
						<el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
					</el-form-item>
					<el-form-item>
						<el-button plain  >时间管理</el-button>
					</el-form-item>
					<el-form-item>
						<el-button plain  >注销</el-button>
					</el-form-item>
				</el-form>
			</el-row>

			<!--列表-->
				<el-row>
	  				<el-col :span="24">
	  					<div class="grid-content bg-purple-dark">
	  						<el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;padding:0 20px 0 20px;">
								<el-table-column type="selection" width="55" >
								</el-table-column>

								<el-table-column prop="userName" label="姓名" width="120" >
								</el-table-column>
								<el-table-column prop="" label="姓名" width="120" >

								</el-table-column>
								<el-table-column label="所属机构" width="200"  prop="orgName" >
								</el-table-column>
								<el-table-column prop="gender" label="性别" width="100" >
								</el-table-column>

								<!-- <el-table-column prop="age" label="年龄" width="100" >
								</el-table-column> -->

								<el-table-column  label="手机号码" width="120" prop="phone">
								</el-table-column>
								<el-table-column prop="" label="专业特长" width="90" >
								</el-table-column>
								<el-table-column prop="" label="上课时间" width="120" >
								</el-table-column>
								<el-table-column prop="" label="教龄(年)" width="80" >
								</el-table-column>
								<el-table-column prop="" label="学历" width="80" >
								</el-table-column>
								<el-table-column prop="" label="工作性质" width="120" prop="jobType">
								</el-table-column>

								
								<el-table-column prop="workStartDate" label="加入日期" width="120" >
								</el-table-column>
								<el-table-column prop="state" label="状态" width="80" >
								</el-table-column>

								<el-table-column label="操作" align="center" width="250" class="el_tep"	style="padding-right:15px;">
									<template scope="scope" >
										<span style="cursor:pointer;">编辑</span>
										<span class="el_tep_d"></span>
										<el-dropdown trigger="click">
									      <span class="el-dropdown-link" style="cursor:pointer;">
									        更多<i class="el-icon-arrow-down el-icon--right"></i>
									      </span>
									      <el-dropdown-menu slot="dropdown">
									        <el-dropdown-item >排课情况</el-dropdown-item>
									        <el-dropdown-item >历史排课</el-dropdown-item>
									        <el-dropdown-item >删除</el-dropdown-item>
									      </el-dropdown-menu>
									    </el-dropdown>
									</template>
								</el-table-column>
							</el-table>
						</div>
					</el-col>
				</el-row>


			<!--工具条-->
			<el-col :span="24" class="toolbar">
				<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
				<!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
				<el-pagination
				  background
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage2"
			      :page-sizes="[10, 30, 50]"
			      :page-size="10"
			      layout="sizes, prev, pager, next"
			      :total="1000"  style="float:right;">
			    </el-pagination>
				
			</el-col>
		</div>
		<el-row>
	        <el-col :span="24">
	          <div >
	                
	            <router-view></router-view>
	          </div>
	        </el-col>
	    </el-row>

		
	</section>
</template>

<script>
  import util from '../../common/js/util'
  import service from '../../common/js/http-service'

  import {getOrgCoachList ,getState,EmployeeDelete} from '../../api/api';


	export default {
		data() {
			return {
				isVisible:true,
				 input:'',
				 options: [{
			          value: '选项1',
			          label: '正常'
			        }, {
			          value: '选项2',
			          label: '注销'
			        }],
			        value: '',
			     options1: [{
			          value: '选项1',
			          label: '研究生'
			        }, {
			          value: '选项2',
			          label: '本科'
			        },{
			          value: '选项3',
			          label: '大专'
			        }],
			        value: '',
			    
				filters: {
					name: ''
				},
				users: [{
					// phone:'15827001324',
					// frequency:'0',
					// RegistrationTime:'2018/02/15 18:30:30'
				}],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

			
				// 标签条
				dynamicTags: ['游泳', '网球'],
        		inputVisible: false,
        		inputValue: ''

			}
		},
		methods: {
			changeMsg(){
		        this.isVisible=false;
		      },
			rowClass({row,rowIndex}){
				console.log(0)
				return 'background:#f1f2f7'  
			},
			
			 handleSizeChange(val) {
			 	this.pagesize = val;
		        return val;
		      },
			
		
			selsChange: function (sels) {
				this.sels = sels;
			},
			//每页显示数据量变更
	        handleSizeChange: function(val) {
	            this.pageSize = val;
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        //页码变更
	        handleCurrentChange: function(val) {
	            this.currentPage = val;
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        getState:function(){
	            getState().then(resp=>{
	                this.states=resp.data.data;
	            });
	        },

	        searchData:function(){
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        loadData: function(currentPage, pageNum){
	            var _this = this ;
	            CourseDelete( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
	            })).then(resp=>{
	                // _this.roles = ;
	                _this.users = resp.data.data;
	                _this.totalCount =resp.data.totalCount;
	            });
	        },
	        toggleSelection(rows) {
	            if (rows) {
	                rows.forEach(row => {
	                    this.$refs.multipleTable.toggleRowSelection(row);
	                });
	            } else {
	                this.$refs.multipleTable.clearSelection();
	            }
	        },
	        handleSelectionChange(val) {
	            this.multipleSelection = val;
	        },
			deleteSelection(){
            this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '删除',
                cancelButtonText: '取消'
            }).then(() => {
                let ids = [];
                const params = new URLSearchParams();
                this.multipleSelection.forEach(item=>{
                    ids.push(item['roleId'])
                    params.append("ids",item['roleId']);
                })
                
                deleteRoles(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message.success("删除成功！");
                        this.searchData();
                    } else {
                        this.$message.error("删除失败！");
                    }
                });
            });

        }
		},
		mounted() {
			this.getState();
			var _this = this ;  //很重要！！

	      getOrgCoachList({}).then(resp=>{
	        console.log(resp);
	        _this.users = resp.data.data;
	      },err=>{
	        console.log(err);
	      }).catch(err=>{
	        console.log(err);
	      })
		}
	}

</script>

<style scoped>
	.sect{
		border:20px solid #f1f2f7;
	}
	.el_f_h{
		min-width:1350px;
	}
	.toolbar{
		min-width:1375px;
		background-color:white;
	}
	<!-- .breadcrumb-container .title{
		display:none;
	} -->
	.el-breadcrumb{
		float:left;
	}
	.cell span{
		color:#0094ff;
		padding-right:5px;
	}
	.el-dropdown{
		background-color:transparent;
		padding-left:3px;
		//border-left:1px solid #E6E9ED;
	}
	.el_tep_d{
		display:inline-block;
		margin-bottom:-2px;
		height:14px;
		border-left:1px solid #E6E9ED;
		}
	.el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
		<!-- background-color:blue !important; -->
		font-size:25px;
	}
	.el_tep{
		padding-right:15px;
	}
	.el-tag + .el-tag {
    margin-left: 10px;
	  }
	.button-new-tag {
	    margin-left: 10px;
	    height: 32px;
	    line-height: 30px;
	    padding-top: 0;
	    padding-bottom: 0;
	  }
	.input-new-tag {
	    width: 90px;
	    margin-left: 10px;
	    vertical-align: bottom;
	  }
</style>


