import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import RegisteredUsers from './views/UserManagement/RegisteredUsers.vue' //注册用户
import BusinessManagementB from './views/UserManagement/BusinessManagementB.vue' //商家管理(B)
import PurchaseRecordsB from './views/UserManagement/forms/PurchaseRecordsB.vue' //购买记录-商家管理(B)
import InvoiceInformationB from './views/UserManagement/forms/InvoiceInformationB.vue' //发票信息-商家管理(B)
import InvoiceAdd from './views/UserManagement/forms/NEW/InvoiceAdd.vue' //新增发票(B)
import ReceivesAnAddressB from './views/UserManagement/forms/ReceivesAnAddressB.vue' //收件地址-商家管理(B)
import BusinessManagementP from './views/UserManagement/BusinessManagementP.vue' //商家管理(P)
import ChildAccount from './views/UserManagement/forms/ChildAccount.vue' //子账号(P)
import PurchaseRecordsP from './views/UserManagement/forms/PurchaseRecordsP.vue' //购买记录-商家管理(P)
import InvoiceInformationP from './views/UserManagement/forms/InvoiceInformationP.vue' //发票信息-商家管理(P)
import ReceivesAnAddressP from './views/UserManagement/forms/ReceivesAnAddressP.vue' //收件地址-商家管理(P)
import NewSubAccountP from './views/UserManagement/forms/NEW/NewSubAccountP.vue' //新增子账号(P)

import AgencyInformation from './views/OrganizationManagement/AgencyInformation.vue' //机构信息
import TheCoachInformation from './views/OrganizationManagement/TheCoachInformation.vue' //教练信息
import CourseInformation from './views/OrganizationManagement/CourseInformation.vue' //课程信息
import StudentInformation from './views/OrganizationManagement/StudentInformation.vue'  //学员信息
import ClassInformation from './views/OrganizationManagement/ClassInformation.vue'  //班级信息
import SiteManagement from './views/OrganizationManagement/SiteManagement.vue'  //场地管理
import ArrangingRules from './views/CoursesArrangingManagement/ArrangingRules.vue'  //排课管理
import CourseArranging from './views/CoursesArrangingManagement/CourseArranging.vue'  //智能排课
import ManualArranging from './views/CoursesArrangingManagement/ManualArranging.vue'  //手工排课
import OrderList from './views/OrderCenter/OrderList.vue'  //订单列表
import RefundOrders from './views/OrderCenter/RefundOrders.vue'  //退款订单
import OrderKanban from './views/OrderCenter/OrderKanban.vue'  //订单看板
import PromotionRules from './views/SalesManagement/PromotionRules.vue'  //促销规则
import ClientReview from './views/After-salesService/ClientReview.vue'  //客户回访
import SatisfactionSurvey from './views/After-salesService/SatisfactionSurvey.vue'  //满意度调查
import OrganizationalStructure from './views/SystemManagement/OrganizationalStructure.vue'  //组织架构
import StaffManagement from './views/SystemManagement/StaffManagement.vue'  //员工管理
import RoleManagement from './views/SystemManagement/RoleManagement.vue'  //角色管理
import SystemParameterConfiguration from './views/SystemManagement/SystemParameterConfiguration.vue'  //系统参数配置
import DataDictionary from './views/SystemManagement/DataDictionary.vue'   //数据字典
import EditSubAccount from './views/UserManagement/forms/EditSubAccount.vue' //编辑子账号
import NewSubAccount from './views/UserManagement/forms/NewSubAccount.vue' //新增子账号
import EditMerchants from './views/UserManagement/forms/EditMerchants.vue'//编辑商家
import NewBusiness from './views/UserManagement/forms/NewBusiness.vue' //新增商家
import UserAgreement from './views/LoginTo/UserAgreement.vue'
//import UserAgreement from './views/charts/UserAgreement.vue'
import register from './views/charts/register.vue'//注冊頁面
import RetrievePassword from './views/charts/RetrievePassword.vue'//找回密码頁面
import PersonalData from './views/charts/PersonalData.vue'//个人资料页面
import ChangePassword from './views/charts/ChangePassword.vue'//修改密码页面
import NewInstitutions from './views/OrganizationManagement/NewlyAdded/NewInstitutions.vue' //新增机构
import CoachInformation from './views/OrganizationManagement/NewlyAdded/CoachInformation.vue' //教练信息（机构信息中）
import NewTrainer from './views/OrganizationManagement/NewlyAdded/NewTrainer.vue' //新增教练
import NewCourses from './views/OrganizationManagement/NewlyAdded/NewCourses.vue' //新增课程  New site
import NewSite from './views/OrganizationManagement/NewlyAdded/NewSite.vue'

import InvoiceInformation from './views/charts/InvoiceInformation.vue'
import PlatformLogin from './views/PlatformLogin.vue'


import echarts from './views/charts/echarts.vue'
import Forms1 from './views/charts/Forms1.vue'
import Forms3 from './views/charts/Forms3.vue'
import Forms4 from './views/charts/Forms4.vue'
import AddRole from './views/SystemManagement/add/AddRole'//新增角色
let routes = [
    {
        path: '/platformLogin',
        component: PlatformLogin,
        name: 'platformLogin',
        hidden: true,
        // childern:[
        //     {path: '/UserAgreement', component: UserAgreement, name: 'UserAgreement', hidden: false }
        // ]
    },
    // {
    //     path: '/platformLogin',
    //     component: PlatformLogin,
    //     name: 'platformLogin',
    //     hidden: true,
    //     // childern:[
    //     //     {path: '/UserAgreement', component: UserAgreement, name: 'UserAgreement', hidden: false }
    //     // ]
    // },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/user-agreement',
        component: UserAgreement,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '表单',
        hidden: true,
        children:[
            { path: '/forms1', component: Forms1, name: 'Forms1', hidden: false },
            { path: '/forms3', component: Forms3, name: 'Forms3', hidden: false },
            { path: '/forms4', component: Forms4, name: 'Forms4', hidden: false },
            /*{ path: '/UserAgreement', component: UserAgreement, name: 'UserAgreement', hidden: false },*/
            { path: '/register', component: register, name: 'register', hidden: false },
            { path: '/RetrievePassword', component: RetrievePassword, name: 'RetrievePassword', hidden: false },
            { path: '/PersonalData', component: PersonalData, name: 'PersonalData', hidden: false },
            { path: '/ChangePassword', component: ChangePassword, name: 'ChangePassword', hidden: false },
            { path: '/InvoiceInformation', component: InvoiceInformation, name: 'InvoiceInformation', hidden: false },
        ]
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '用户管理',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/main', component: Main, name: '主页', hidden: true },
            { path: '/RegisteredUsers', component: RegisteredUsers, name: '注册用户'},
            { path: '/BusinessManagementB', component: BusinessManagementB, name: '商家管理(B)',
                 children: 
                    [
                        {path:'EditSubAccount', component:EditSubAccount, name:'编辑子账号'},
                        {path:'NewSubAccount', component:NewSubAccount, name:'新增子账号'},
                        {path:'PurchaseRecordsB', component:PurchaseRecordsB, name:'购买记录'},
                        {path:'InvoiceInformationB', component:InvoiceInformationB, name:'发票信息',
                            children:
                                [
                                    {path:'InvoiceAdd', component:InvoiceAdd, name:'新增发票'}
                                  ]  

                    },
                        {path:'ReceivesAnAddressB', component:ReceivesAnAddressB, name:'收件地址'}

                ]

        },
            { path: '/BusinessManagementP', component: BusinessManagementP, name: '商家管理(P)',
                 children: 
                    [
                        {path:'EditMerchants', component:EditMerchants, name:'编辑商家'},
                        {path:'NewBusiness', component:NewBusiness, name:'新增商家'},
                        {path:'ChildAccount', component:ChildAccount, name:'子账号',
                            children:
                           [
                                {path:'NewSubAccountP', component:NewSubAccountP, name:'新增子账号(P端)'}
                           ] 
                    },
                        {path:'PurchaseRecordsP', component:PurchaseRecordsP, name:'购买记录'},
                        {path:'InvoiceInformationP', component:InvoiceInformationP, name:'发票信息'},
                        {path:'ReceivesAnAddressP', component:ReceivesAnAddressP, name:'收件地址'}
                ]

            },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '机构管理',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/AgencyInformation', component: AgencyInformation, name: '机构信息' ,
                 children: 
                    [
                       {path:'NewInstitutions', component:NewInstitutions, name:'新增机构'},
                       {path:'CoachInformation', component:CoachInformation, name:'教练信息(机构中)'},
                ],

        },
            { path: '/TheCoachInformation', component:TheCoachInformation, name: '教练信息' ,
                 children:
                     [
                       {path:'NewTrainer', component:NewTrainer, name:'新增教练'},
                ],
        },
            { path: '/CourseInformation', component: CourseInformation, name: '课程信息' ,
                 children:
                     [
                       {path:'NewCourses', component:NewCourses, name:'新增课程'},
                ],

        },
            { path: '/StudentInformation', component: StudentInformation, name: '学员信息' },
            { path: '/ClassInformation', component: ClassInformation, name: '班级信息' , 
                 children:
                     [
                       {path:'NewSite', component:NewSite, name:'新增场地'},
                ],
        },
            { path: '/SiteManagement', component: SiteManagement, name: '场地管理' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/page6', component: Page6, name: '导航三' }
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '排课管理',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/ArrangingRules', component: ArrangingRules, name: '排课规则' },
            { path: '/CourseArranging', component: CourseArranging, name: '智能排课' },
            { path: '/ManualArranging', component: ManualArranging, name: '手工排课' },
            
            //{ path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '订单中心',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/OrderList', component: OrderList, name: '订单列表' },
            { path: '/RefundOrders', component: RefundOrders, name: '退款订单' },
            { path: '/OrderKanban', component: OrderKanban, name: '订单看板' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '促销管理',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/PromotionRules', component: PromotionRules, name: '促销规则' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '售后服务',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/ClientReview', component: ClientReview, name: '客户回访' },
            { path: '/SatisfactionSurvey', component:SatisfactionSurvey, name: '满意度调查' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '系统管理',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/OrganizationalStructure', component: OrganizationalStructure, name: '组织架构' },
            { path: '/StaffManagement', component: StaffManagement, name: '员工管理' },
            { path: '/RoleManagement', component: RoleManagement, name: '角色管理',
                children:
                    [
                        {path:'addRole', component:AddRole, name:'新增角色'},
                    ],

            },

            { path: '/SystemParameterConfiguration', component: SystemParameterConfiguration, name: '系统参数配置' },
            { path: '/DataDictionary', component: DataDictionary, name: '数据字典' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


