<!-- 个人资料页面 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="联系人信息" >
					<el-input v-model="form.name" style="width:140px;padding-right:10px;" placeholder="填写联系人姓名"></el-input>
					<el-select v-model="form.region" placeholder="请选择性别" style="width:200px;">
						<el-option label="男" value="man"></el-option>
						<el-option label="女" value="woman"></el-option>
					</el-select>
					<el-input v-model="form.name" style="width:140px;padding-left:10px;" placeholder="填写联系人手机号"></el-input>
				</el-form-item>
				<el-form-item label="头像">
					<el-upload
					  class="avatar-uploader"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="注册日期">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="生效日期">

					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="期限(月)">

					<el-input-number v-model="num1" @change="handleChange" :min="1" :max="10" label="描述文字"></el-input-number>
				</el-form-item>
				<el-form-item label="失效日期">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>

				<el-form-item label="营业执照">
					<el-upload
					  action="https://jsonplaceholder.typicode.com/posts/"
					  list-type="picture-card"
					  :on-preview="handlePictureCardPreview"
					  :on-remove="handleRemove">
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>

				<el-form-item label="审核状态">
					<el-select v-model="form.region" placeholder="通过" style="width:200px;">
						<el-option label="是" ></el-option>
						<el-option label="否" ></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary">提交</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					imageUrl: '',
					num1:1,
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			 handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
			onSubmit() {
				console.log('submit!');
			},
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('上传头像图片只能是 JPG 格式!');
		        }
		        if (!isLt2M) {
		          this.$message.error('上传头像图片大小不能超过 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      }
		    }
		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


