<!-- 新增子账号 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="子账号信息" >
					<el-input v-model="form.name" style="width:140px;padding-right:10px;"></el-input>
					<el-select v-model="form.region" placeholder="请选择活动区域" style="width:200px;">
						<el-option label="区域一" value="shanghai"></el-option>
						<el-option label="区域二" value="beijing"></el-option>
					</el-select>
					<el-input v-model="form.name" style="width:140px;padding-left:10px;"></el-input>
					<el-input v-model="form.name" style="width:340px;padding-right:10px;padding-top:20px;"></el-input>
					<el-input v-model="form.name" style="width:155px"></el-input>
				</el-form-item>
				<el-form-item label="所属机构">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="头像">
					<!-- <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch> -->
					<el-upload
					  class="avatar-uploader"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="注册日期">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="生效日期">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="期限">
					<el-input-number v-model="num1" @change="handleChange" :min="1" :max="10" label="描述文字"></el-input-number>
				</el-form-item>
				<el-form-item label="失效日期">
					<el-input v-model="form.name" style="width:98%"></el-input>
				</el-form-item>
				<el-form-item label="账号类型">
					<!-- 暂时空缺 -->
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary">提交</el-button>
					<el-button @click.native.prevent>重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
	 import {getPlatformShopkeeperAdd} from "../../../api/api"
	export default {
		data() {
			return {
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					imageUrl: '',
					num1:1,
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			},
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('上传头像图片只能是 JPG 格式!');
		        }
		        if (!isLt2M) {
		          this.$message.error('上传头像图片大小不能超过 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      }
		    }
		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


