						</el-form-item>


						<el-form-item style="width:300px;">
							<el-col :span="11">
								<el-input v-model="filters.name3" ></el-input>
							</el-col>
							<el-col class="line" :span="2" style="padding-left:10px;">-</el-col>
							<el-col :span="11">
								<el-input v-model="filters.name4" ></el-input>
							</el-col>
						</el-form-item>
						<!-- 时间选择器O -->
						<el-form-item >
							<div class="block">
							    <el-date-picker
							      v-model="value6"
							      type="daterange"
							      range-separator="至"
							      start-placeholder="开始日期"
							      end-placeholder="结束日期">
							    </el-date-picker>
							  </div>
						</el-form-item>
						<!-- 时间选择器E -->
