							<el-button type="primary" v-on:click="searchData">搜索</el-button>
						</el-form-item>
						<div class="el-icon" style="width:200px;float:right;"> 
								<el-button plain icon="el-icon-refresh"></el-button>
								<el-button plain icon="el-icon-download"></el-button>
								<el-button plain icon="el-icon-setting"></el-button>	
						</div> 
					</div>
					<el-form-item>
						<router-link to="/CourseInformation/NewCourses">
				                  <span style="cursor:pointer;" v-on:click="changeMsg">
				                      <el-button type="primary"  icon="el-icon-plus" >
				                          新增
				                      </el-button>
				                  </span>
		                </router-link>
					</el-form-item>
					<el-form-item>
						<el-button type="danger" @click="deleteSelection" style="background-color:#EE1313;">批量删除</el-button>
					</el-form-item>
					<el-form-item>
						<el-button plain >启用</el-button>
					</el-form-item>
					<el-form-item>
						<el-button plain  >禁用</el-button>
					</el-form-item>
				</el-form>
			</el-row>

			<!--列表-->
				<el-row>
	  				<el-col :span="24">
	  					<div class="grid-content bg-purple-dark">
	  						<el-table :data="users" :header-cell-style="rowClass" highlight-current-row v-loading="listLoading" @selection-change="handleSelectionChange" style="width: 100%;padding:0 20px 0 20px;">
								<el-table-column type="selection" width="55" >
								</el-table-column>
								<el-table-column  label="课程名称" width="120" prop="courseName">
								</el-table-column>
								<el-table-column  label="所属机构" width="100" prop="orgName">
								</el-table-column>

								<el-table-column  label="课程大类" width="90" prop="firstCategoryName">
								</el-table-column>
								<el-table-column  label="课程项目" width="90" prop="secondCategoryName">
								</el-table-column>

								<!-- <el-table-column prop="addr" label="地址" min-width="180" >
								</el-table-column> -->

								<el-table-column  label="课程类型" width="90" >
								</el-table-column>
								<el-table-column  label="班种" width="120" prop="classTypeName">
								</el-table-column>
								<el-table-column  label="课时种类" width="90" >
								</el-table-column>
								<el-table-column  label="上课时间" width="120" >
								</el-table-column>
								<el-table-column  label="价格" width="120" >
								</el-table-column>
								<el-table-column  label="开始日期" width="120" prop="createTime">
								</el-table-column>
								<el-table-column  label="状态" width="90" prop="state">
								</el-table-column>

								<el-table-column label="操作" align="center" width="250" class="el_tep"	style="padding-right:15px;">
									<template scope="scope" >
										<span style="cursor:pointer;">编辑</span>
										<span class="el_tep_d"></span>
										<el-dropdown trigger="click">
									      <span class="el-dropdown-link" style="cursor:pointer;">
									        更多<i class="el-icon-arrow-down el-icon--right"></i>
									      </span>
									      <el-dropdown-menu slot="dropdown">
									        <el-dropdown-item >商户申请</el-dropdown-item>
									        <el-dropdown-item >消费记录</el-dropdown-item>
									        <el-dropdown-item >发票信息</el-dropdown-item>
									        <el-dropdown-item >收件地址</el-dropdown-item>
									        <el-dropdown-item >删除</el-dropdown-item>
									      </el-dropdown-menu>
									    </el-dropdown>
									</template>
								</el-table-column>
							</el-table>
						</div>
					</el-col>
				</el-row>


			<!--工具条-->
			<el-col :span="24" class="toolbar">
				<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
				<!-- <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;"> </el-pagination>-->
				<el-pagination
				  background
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage"
			      :page-sizes="[10, 30, 50]"
			      :page-size="10"
			      layout="sizes, prev, pager, next"
			      :total="totalCount"  style="float:right;">
			    </el-pagination>
				
			</el-col>
		</div>
		<el-row>
	        <el-col :span="24">
	          <div >
	                
	            <router-view></router-view>
	          </div>
	        </el-col>
	    </el-row>
	</section>
</template>


<script>
   import util from '../../common/js/util'
   import service from '../../common/js/http-service'

  import axios from 'axios';
  import {getOrgCourseList ,getState , CourseDelete } from '../../api/api';
  import qs from 'qs'

	export default {
		data() {
			return {
				isVisible:true,
				totalCount: 0,
          		currentPage: 1,
				 options: [{
			          value: '选项1',
			          label: '启用'
			        }, {
			          value: '选项2',
			          label: '禁用'
			        }],
			        value: '',
			     options: [{
			          value: '选项1',
			          label: '小程序'
			        }, {
			          value: '选项2',
			          label: 'IOS'
			        },{
			          value: '选项3',
			          label: 'Android'
			        },{
			          value: '选项4',
			          label: 'PC'
			        }],
			        value: '',
			     options: [{
			          value: '选项1',
			          label: '男'
			        }, {
			          value: '选项2',
			          label: '女'
			        }],
			        value: '',
				filters: {
					name: ''
				},
				users: [{
					phone:'15827001324',
					frequency:'0',
					RegistrationTime:'2018/02/15 18:30:30'
				}],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列


					pageSize:10,
			        states:[],
			        state:[],
				
			

				
				//时间选择器
				  pickerOptions2: {
			          shortcuts: [{
			            text: '最近一周',
			            onClick(picker) {
			              const end = new Date();
			              const start = new Date();
			              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', [start, end]);
			            }
			          }, {
			            text: '最近一个月',
			            onClick(picker) {
			              const end = new Date();
			              const start = new Date();
			              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
			              picker.$emit('pick', [start, end]);
			            }
			          }, {
			            text: '最近三个月',
			            onClick(picker) {
			              const end = new Date();
			              const start = new Date();
			              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
			              picker.$emit('pick', [start, end]);
			            }
			          }]
			        },
			        value6: '',

			}
		},
		methods: {
			changeMsg(){
		        this.isVisible=false;
		      },
			rowClass({row,rowIndex}){
				console.log(0)
				return 'background:#f1f2f7'  
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//每页显示数据量变更
	        handleSizeChange: function(val) {
	            this.pageSize = val;
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        //页码变更
	        handleCurrentChange: function(val) {
	            this.currentPage = val;
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        getState:function(){
	            getState().then(resp=>{
	                this.states=resp.data.data;
	            });
	        },

	        searchData:function(){
	            this.loadData( this.currentPage, this.pageSize);
	        },
	        loadData: function(currentPage, pageNum){
	            var _this = this ;
	            CourseDelete( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
	            })).then(resp=>{
	                // _this.roles = ;
	                _this.users = resp.data.data;
	                _this.totalCount =resp.data.totalCount;
	            });
	        },
	        toggleSelection(rows) {
	            if (rows) {
	                rows.forEach(row => {
	                    this.$refs.multipleTable.toggleRowSelection(row);
	                });
	            } else {
	                this.$refs.multipleTable.clearSelection();
	            }
	        },
	        handleSelectionChange(val) {
	            this.multipleSelection = val;
	        },
	        //批量删除
	        deleteSelection(){
	            this.$confirm('您确定是否删除数据，删除数据后将无法恢复？', '确认信息', {
	                distinguishCancelAndClose: true,
	                confirmButtonText: '删除',
	                cancelButtonText: '取消'
	            }).then(() => {
	                let ids = [];
	                const params = new URLSearchParams();
	                this.multipleSelection.forEach(item=>{
	                    ids.push(item['roleId'])
	                    params.append("ids",item['roleId']);
	                })
	                CourseDelete(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
	                    if (resp.resultCode == 0) {
	                        this.$message.success("删除成功！");
	                        this.searchData();
	                    } else {
	                        this.$message.error("删除失败！");
	                    }
	                });
	            });

	        },
		
		},
		
		mounted() {
		//this.loadData(this.currentPage, this.pageSize);
        this.getState();

		  var _this = this ;  
	      getOrgCourseList({}).then(resp=>{

	        console.log(resp);
	        _this.users = resp.data.data;
	      },err=>{
	        console.log(err);
	      }).catch(err=>{
	        console.log(err);
	      })
		 }
	}

</script>

<style scoped>
	
	.el_f_h{
		min-width:1350px;
	}
	.toolbar{
		min-width:1375px;
		background-color:white;
	}
	<!-- .breadcrumb-container .title{
		display:none;
	} -->
	.el-breadcrumb{
		float:left;
	}
	.cell span{
		color:#0094ff;
		padding-right:5px;
	}
	.el-dropdown{
		background-color:transparent;
		padding-left:3px;
		//border-left:1px solid #E6E9ED;
	}
	.el_tep_d{
		display:inline-block;
		margin-bottom:-2px;
		height:14px;
		border-left:1px solid #E6E9ED;
		}
	.el-table .el-table__header-wrapper .el-table__header .has-gutter tr{
		<!-- background-color:blue !important; -->
		font-size:25px;
	}
	.el_tep{
		padding-right:15px;
	}
</style>


