<!-- 新增场地 -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="场地名称">
					<el-input v-model="form.placeName" style="width:98%" placeholder="给场地起个名字"></el-input>
				</el-form-item>
				<el-form-item label="场地设施">
					 <el-select  multiple placeholder="请选择" style="width:98%">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>

				<el-form-item label="所属权" >
					<el-select  placeholder="选择所属权" style="width:200px;">
						 <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
					<el-input  style="width:200px;" placeholder="建筑面积"></el-input>
				</el-form-item>
				<el-form-item label="使用期限" >
					<div class="block">				    
					<el-date-picker
				      v-model="value3"
				      type="datetimerange"
				      range-separator="至"
				      start-placeholder="开始日期"
				      end-placeholder="结束日期">
				    </el-date-picker>
				  </div>
				</el-form-item>
				<el-form-item label="所在地区" >
					<!-- 缺少省市联动 -->
					<el-input v-model="form.area" style="width:98%" placeholder="其他信息"></el-input>
					<!-- 缺少地图 -->
				</el-form-item>


				
				
				<el-form-item label="场地说明">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"  @click="onSubmit">提交</el-button>
					<el-button plain>重置</el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
	import {PlaceAdd} from "../../../api/api"
	export default {
		data() {
			return {
				  pickerOptions2: {
		          shortcuts: [{
		            text: '最近一周',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
		              picker.$emit('pick', [start, end]);
		            }
		          }, {
		            text: '最近一个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
		              picker.$emit('pick', [start, end]);
		            }
		          }, {
		            text: '最近三个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
		              picker.$emit('pick', [start, end]);
		            }
		          }]
		        },
		        value3: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
		       
				
				 pickerOptions1: {
			          disabledDate(time) {
			            return time.getTime() > Date.now();
			          },
			          shortcuts: [{
			            text: '今天',
			            onClick(picker) {
			              picker.$emit('pick', new Date());
			            }
			          }, {
			            text: '昨天',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24);
			              picker.$emit('pick', date);
			            }
			          }, {
			            text: '一周前',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', date);
			            }
			          }]
			        },
			        value1: '',
				 options: [{
	                value: '选项1',
	                label: '羽毛球馆'
	              }, {
	                value: '选项2',
	                label: '游泳馆'
	              }],
	              value: '',
	            options2: [{
	                value: '选项1',
	                label: '自由'
	              }, {
	                value: '选项2',
	                label: '租赁'
	              }],
	              value5: [],
	         
				form: {
					placeName:'',
					area:''
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			 handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
			
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('上传头像图片只能是 JPG 格式!');
		        }
		        if (!isLt2M) {
		          this.$message.error('上传头像图片大小不能超过 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      },
		     	onSubmit:function() {
	                PlaceAdd({placeName:this.form.placeName,area:this.form.area}).then(resp=>{
	                    if (resp.resultCode == 0) {
	                        this.$message({
	                            message:"添加成功！",
	                            type: "success"
	                        });
	                        this.showTableView();
	                    } else {
	                        this.$message({
	                            message:resp.message,
	                            type: 'error'
	                        });
	                    }
	                });
			  },
		    }
		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


