import axios from 'axios';
import qs from 'qs' // 序列化请求数据，视服务端的要求


axios.defaults.timeout = 50000;
// axios.defaults.baseURL = 'http://okycz.com';
axios.defaults.baseURL = process.env.API_ROOT

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

function strMapToObj(strMap) {
    let obj = Object.create(null);
    for (let [k,v] of strMap) {
        // We don’t escape the key '__proto__'
        // which can cause problems on older engines
        obj[k] = v;
    }
    return obj;
}

//http request 拦截器
axios.interceptors.request.use(
  config => {
        // Tip: 1
        // 请求开始的时候可以结合 vuex 开启全屏的 loading 动画
        // Tip: 2
        // 带上 token , 可以结合 vuex 或者重 localStorage
        // if (store.getters.token) {
        //     config.headers['X-Token'] = getToken() // 让每个请求携带token--['X-Token']为自定义key 请根据实际情况自行修改
        // } else {
        //     // 重定向到登录页面
        // }

        // Tip: 3
        // 根据请求方法，序列化传来的参数，根据后端需求是否序列化
        if (config.method.toLocaleLowerCase() === 'post'
          || config.method.toLocaleLowerCase() === 'put'
          || config.method.toLocaleLowerCase() === 'delete') {


            if(config.hasOwnProperty("isArray")){
                let name = config.arrayId;
                const map = new Map([
                    [name, config.data]
                ]);
                let data = strMapToObj(map)

                config.data = qs.stringify(data,{arrayFormat: 'repeat'});
            }
            else
            if(typeof config.data === 'object'){
                // console.log("objedddddddddddd")
                config.data = qs.stringify(config.data);
            }

        }
      return config;

  },
  error => {
    return Promise.reject(err);
  }
);

//响应拦截器即异常处理
axios.interceptors.response.use(response => {
    return response
}, err => {
    console.log(err)
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
            console.log('错误请求')
          break;
        case 401:
            console.log('未授权，请重新登录')
          break;
        case 403:
          console.log('拒绝访问')
          break;
        case 404:
          console.log('请求错误,未找到该资源')
          break;
        case 405:
          console.log('请求方法未允许')
          break;
        case 408:
          console.log('请求超时')
          break;
        case 500:
          console.log('服务器端出错')
          break;
        case 501:
          console.log('网络未实现')
          break;
        case 502:
          console.log('网络错误')
          break;
        case 503:
          console.log('服务不可用')
          break;
        case 504:
          console.log('网络超时')
          break;
        case 505:
          console.log('http版本不支持该请求')
          break;
        default:
          console.log(`连接错误${err.response.status}`)
      }
    } else {
      console.log('连接到服务器失败')
    }
    return Promise.reject(err);
})


export function setAuthorization(authorization){
    axios.defaults.headers.common['Authorization'] = authorization ;//#sessionStorage.getItem("authorization")
    console.log("authorization");
}



/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {},config={}){
    return new Promise((resolve,reject) => {
        axios.post(url,data,config)
            .then(response => {
                // console.log(response);
                resolve(response.data);
            },err => {
                reject(err)
            }).catch(err => {
            reject(err)
        });
    })
}
export function postResp(url,data = {}){
    return new Promise((resolve,reject) => {
        axios.post(url,data)
            .then(response => {
                // console.log(response);
                resolve(response);
            },err => {
                reject(err)
            }).catch(err => {
            reject(err)
        });
    })
}

 /**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
               reject(err)
           }).catch(err => {
               reject(err)
           });
  })
}

 /**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
               reject(err)
           }).catch(err => {
               reject(err)
           });
  })
}

export default {
    put,
    patch,
    get,
    post,
    postResp,
    setAuthorization
}



